(function () {
    'use strict';

    angular
        .module('app.rvlisting', ['angularSoap', 'mwl.confirm'])
        .config(config);

    /** @ngInject */
    function config($stateProvider, $translatePartialLoaderProvider, $locationProvider) {
        // State
        $stateProvider
            .state('app.rvlisting', {
                url: '/rv-listing',
                views: {
                    'main@': {
                        controller: 'RVListingController as vm',
                        templateUrl: 'app/rvlisting/views/create/rvlisting.html'
                    },
                    'header@': {
                        controller: 'HeaderController as vm',
                        templateUrl: 'app/header/header.html'
                    }
                },
                protect: true
            })
            .state('app.rvlisting.step1', {
                url: '/basics',
                templateUrl: 'app/rvlisting/views/create/rvlisting-basic.html',
                controller: 'ListAnRVStep1Controller',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.rvlisting.step2', {
                url: '/location',
                templateUrl: 'app/rvlisting/views/create/rvlisting-location.html',
                controller: 'ListAnRVStep2Controller',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.rvlisting.step3', {
                url: '/details',
                templateUrl: 'app/rvlisting/views/create/rvlisting-details.html',
                controller: 'ListAnRVStep3Controller',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.rvlisting.step4', {
                url: '/pricing',
                templateUrl: 'app/rvlisting/views/create/rvlisting-pricing.html',
                controller: 'ListAnRVStep4Controller',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.rvlisting.step5', {
                url: '/photos',
                templateUrl: 'app/rvlisting/views/create/rvlisting-photos.html',
                controller: 'ListAnRVStep5Controller',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.rvlisting.success', {
                url: '/success',
                views: {
                    'success': {
                        templateUrl: 'app/rvlisting/views/create/rvlisting-success.html',
                        controller: 'ListAnRVSuccessController',
                        controllerAs: 'vm',
                        protect: true
                    }
                },
                protect: true
            })
            .state('app.edit-rv', {
                url: '/edit-rv/:id',
                views: {
                    'main@': {
                        controller: 'EditRVController',
                        controllerAs: 'vm',
                        templateUrl: 'app/rvlisting/views/edit/edit-rv.html'
                    },
                    'header@': {
                        controller: 'HeaderController as vm',
                        templateUrl: 'app/header/header.html'
                    }
                },
                protect: true
            })
            .state('app.edit-rv.basics', {
                url: '/basics',
                templateUrl: 'app/rvlisting/views/edit/basic.edit-rv.html',
                controller: 'EditRVBasicsController',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.edit-rv.location', {
                url: '/location',
                templateUrl: 'app/rvlisting/views/edit/location.edit-rv.html',
                controller: 'EditRVLocationController',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.edit-rv.success-commit-insurance', {
                url: '/success',
                templateUrl: 'app/rvlisting/views/edit/insurance-success-validate.html',
                controller: 'EditRVDetailsController',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.edit-rv.details', {
                url: '/details',
                templateUrl: 'app/rvlisting/views/edit/details.edit-rv.html',
                controller: 'EditRVDetailsController',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.edit-rv.pricing', {
                url: '/pricing',
                templateUrl: 'app/rvlisting/views/edit/pricing.edit-rv.html',
                controller: 'EditRVPricingController',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.edit-rv.photos', {
                url: '/photos',
                templateUrl: 'app/rvlisting/views/edit/photos.edit-rv.html',
                controller: 'EditRVPhotosController',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.edit-rv.availability', {
                url: '/availability',
                templateUrl: 'app/rvlisting/views/edit/availability.edit-rv.html',
                controller: 'EditRVAvailabilityController',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.edit-rv.insurance', {
                url: '/insurances',
                templateUrl: 'app/rvlisting/views/edit/insurance.edit-rv.html',
                controller: 'EditRVInsuranceController',
                controllerAs: 'vm',
                protect: true
            })
            .state('app.edit-rv.publish', {
                url: '/publish',
                templateUrl: 'app/rvlisting/views/edit/publish.edit-rv.html',
                controller: 'EditRVPublishController',
                controllerAs: 'vm',
                protect: true
            });        

        // Translation
        $translatePartialLoaderProvider.addPart('app/rvlisting');

        $locationProvider.html5Mode(true);

    }
})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('EditRVPublishController', EditRVPublishController);

    /** @ngInject */
    function EditRVPublishController($state, $scope, RVListingService, $translate) {
        var vm = this;

        // form actions


        vm.togglePublish = function (isPublish) {
            if (isPublish === $scope.rv.IsPublish) {
                return;
            }

            if (isPublish) {
                publish();
            } else {
                unpublish();
            }
        }

        function publish() {
            $scope.loading.processingRV = true;

            RVListingService.publish($scope.rv.Id).then(function () {

                $scope.loading.processingRV = false;

                $scope.rv.IsPublish = true;

                $translate('RV.EDIT.PUBLISH.PUBLISHED').then(function (msg) {
                    alert(msg);
                })


            }, function (errors) {
                $scope.loading.processingRV = false;

                if (errors && errors.Message) {
                    alert(errors.Message);
                } else {
                    $translate('RV.EDIT.BUSY').then(function (msg) {
                        alert(msg);
                    })
                }
            })
        }

        function unpublish() {
            $scope.loading.processingRV = true;

            RVListingService.unpublish($scope.rv.Id).then(function () {

                $scope.loading.processingRV = false;

                $scope.rv.IsPublish = false;

                $translate('RV.EDIT.PUBLISH.UNPUBLISHED').then(function (msg) {
                    alert(msg);
                })

            }, function (errors) {
                $scope.loading.processingRV = false;

                if (errors && errors.Message) {
                    alert(errors.Message);
                } else {
                    $translate('RV.EDIT.BUSY').then(function (msg) {
                        alert(msg);
                    })
                }
            })
        }

        // end form actions
    }

})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('EditRVPricingController', EditRVPricingController);

    /** @ngInject */
    function EditRVPricingController($state, $scope, RVListingService) {
        var vm = this;

        // need to clone an rv to this view scope for edit
        // call shared method from master view (from edit-rv.controller.js)
        $scope.__getCloneRV().then(function (rv) {
            vm.rv = rv;
            __watchHSDateChanges();
        })

        // data
        
        /* options */

        vm.dateMonthPickerOptions = {
            maxMode: 'day',
            formatDayTitle: 'MMMM'
        }

        /* end options */

        // end data

        // on data change handlers

        /* handle HS Date changed */

        function __watchHSDateChanges() {
            $scope.$watch(function () {
                return vm.rv.HSStartDate
            }, function (val, old) {
                if (val != old) {
                    vm.onHSStartDateChange();
                }
            })

            $scope.$watch(function () {
                return vm.rv.HSEndDate
            }, function (val, old) {
                if (val != old) {
                    vm.onHSEndaDateChange();
                }
            })
        }

        vm.onHSStartDateChange = function () {
            if (vm.rv.HSStartDate && vm.rv.HSEndDate) {
                if (vm.rv.HSStartDate > vm.rv.HSEndDate) {
                    vm.rv.HSEndDate = angular.copy(vm.rv.HSStartDate);
                }
            }
        }

        vm.onHSEndaDateChange = function () {
            if (vm.rv.HSStartDate && vm.rv.HSEndDate) {
                if (vm.rv.HSStartDate > vm.rv.HSEndDate) {
                    vm.rv.HSStartDate = angular.copy(vm.rv.HSEndDate);
                }
            }
        }
        /* end handle HS Date changed */

        vm.onMileageChanged = function () {
            if (!vm.rv.IsChargeMileage) {
                vm.rv.DailyKMAllowed = null;
                vm.rv.SurchargePerExtraKM = null;
            }
        }

        vm.onGeneratorChanged = function () {
            if (!vm.rv.IsGenerator) {
                vm.rv.FreeHoursPerDay = null;
                vm.rv.ChargePerHourOver = null;
            }
        }

        vm.onFreeHoursPerDayChanged = function () {
            if (vm.rv.FreeHoursPerDay != null && vm.rv.FreeHoursPerDay != undefined) {
                if (vm.rv.FreeHoursPerDay == 24) {
                    vm.rv.ChargePerHourOver = null;
                }
            } else {
                vm.rv.ChargePerHourOver = null;
            }
        }

        // end on data change handlers

        // form actions

        vm.saveRV = function () {

            if ($scope.rvPricingForm.$invalid) {

                angular.forEach($scope.rvPricingForm.$error, function (field) {
                    angular.forEach(field, function (errorField) {
                        errorField.$setTouched();
                    })
                });

                return;
            }

            vm.onFreeHoursPerDayChanged();

            // call master view (from edit-rv.controller.js) method 
            $scope.__save(vm.rv);

        }

        // end form actions
    }

})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('EditRVPhotosController', EditRVPhotosController);

    /** @ngInject */
    function EditRVPhotosController($state, $scope, RVListingService, SVCS, $sce, $translate) {
        var vm = this;
        
        // init view

        vm.SVCS = SVCS;

        $translate('RV.EDIT.PHOTOS.MORE_INFO_POPOVER').then(function (msg) {
            vm.moreInfoPopover = $sce.trustAsHtml(msg);
        })

        // end init view

        // on data change handlers

        vm.onOrderChanged = function (newIndex) {

            $scope.loading.processingRV = true;
            RVListingService.setPhotoOrder($scope.rv.Photos[newIndex].Id, newIndex).then(function () {

                $scope.loading.processingRV = false;
                angular.forEach($scope.rv.Photos, function (p, idx) {
                    p.Order = idx;
                })

            }, function (errors) {

                $scope.loading.processingRV = false;
                if (errors && errors.Message) {
                    alert(errors.Message);
                } else {
                    $translate('RV.EDIT.BUSY').then(function (msg) {
                        alert(msg);
                    })
                }

            })

        }

        // end data change handlers
              
        // form actions

        vm.removePhoto = function (photo, idx) {

            if ($scope.rv.Photos.length == 1) {

                $translate("RV.EDIT.PHOTOS.ERRORS.REQUIRED_ONE_FILE").then(function (msg) {
                    alert(msg);
                })

                return;
            }

            $scope.loading.processingRV = true;
            RVListingService.removePhoto(photo.Id).then(function () {

                $scope.loading.processingRV = false;
                $scope.rv.Photos.splice(idx, 1);

            }, function (errors) {

                $scope.loading.processingRV = false;
                if (errors && errors.Message) {
                    alert(errors.Message);
                } else {
                    $translate('RV.EDIT.BUSY').then(function (msg) {
                        alert(msg);
                    })
                }

            })

        }

        vm.uploadPhoto = function (files, invalidFiles) {

            if (invalidFiles.length) {
                $translate('RV.EDIT.PHOTOS.ERRORS.FILE_INVALID', { name: invalidFiles[0].name }).then(function (msg) {
                    alert(msg);
                })

                return;
            }

            if (!files || !files.length) {
                return;
            }

            $scope.loading.processingRV = true;

            sequentialUpload(files, 0, [], sequentialUploadCompleted);
        }

        function sequentialUploadCompleted(errorsFiles) {
            var filesNames = [];
            if (errorsFiles && errorsFiles.length) {
                angular.forEach(errorsFiles, function (item) {
                    filesNames.push(errorsFiles.file.name);
                })
            }

            if (filesNames.length) {
                $translate('RV.EDIT.PHOTOS.ERRORS.UPLOAD_ERRORS', { names: filesNames.join(', ') }).then(function (msg) {
                    alert(msg);
                })
            }
        }



        function sequentialUpload(files, i, errorsFiles, callback) {
            RVListingService.uploadPhoto($scope.rv.Id, files[i]).then(function (photo) {

                if (!$scope.rv.Photos) {
                    $scope.rv.Photos = [];
                }

                $scope.rv.Photos.push(photo);

            }, function (error) {
                errorsFiles.push({ file: files[i], error: error });

            }).then(function () {
                if (files.length === i + 1) {
                    $scope.loading.processingRV = false;
                    callback(errorsFiles);
                } else {
                    sequentialUpload(files, i + 1, errorsFiles, callback);
                }
            });
        }

        // end form actions
    }

})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('EditRVLocationController', EditRVLocationController);

    /** @ngInject */
    function EditRVLocationController($state, $scope, RVListingService, $translate) {
        var vm = this;

        // need to clone an rv to this view scope for edit
        // call shared method from master view (from edit-rv.controller.js)
        $scope.__getCloneRV().then(function (rv) {
            vm.rv = rv;
        })

        // data

        vm.provinces = [
            { "Name": "Alberta" },
            { "Name": "British Columbia" },
            { "Name": "Manitoba" },
            { "Name": "New Brunswick" },
            { "Name": "Newfoundland and Labrador" },
            { "Name": "Nova Scotia" },
            { "Name": "Ontario" },
            { "Name": "Prince Edward Island" },
            { "Name": "Quebec" },
            { "Name": "Saskatchewan" },
            { "Name": "Northwest Territories" },
            { "Name": "Nunavut" },
            { "Name": "Yukon" }
        ];

        // end data

        // form actions

        vm.saveRV = function () {

            if ($scope.rvLocationForm.$invalid) {

                angular.forEach($scope.rvLocationForm.$error, function (field) {
                    angular.forEach(field, function (errorField) {
                        errorField.$setTouched();
                    })
                });

                return;
            }


            var parts = [vm.rv.Location.Street, vm.rv.Location.City, vm.rv.Location.State, 'Canada'];

            RVListingService.getCoordinateFromAddressAsync(parts).then(function (coords) {

                vm.rv.Location.Latitude = coords.lat;
                vm.rv.Location.Longitude = coords.lng;

                // call master view (from edit-rv.controller.js) method 
                $scope.__save(vm.rv);

            }, function () {
                $translate('RV.EDIT.LOCATION.ERRORS.ADDRESS_UNRECOGNIZE').then(function (msg) {
                    alert(msg);
                })
            })

        }

        // end form actions
    }

})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('EditRVInsuranceController', EditRVInsuranceController);

    /** @ngInject */
    function EditRVInsuranceController($state, $scope, $rootScope, RVListingService, SVCS, $translate) {
        var vm = this;

        // need to clone an rv to this view scope for edit
        // call shared method from master view (from edit-rv.controller.js)
        $scope.__getCloneRV().then(function (rv) {
            vm.rv = rv;

            if (rv.Location.State != 'Ontario' || (rv.RentalType != $rootScope.$rentalType.TravelTrailer.Value && rv.RentalType != $rootScope.$rentalType.MotorHome.Value)) {
                $state.go('app.edit-rv.basics');
                return;
            }

            if (!vm.rv.InsuranceProfile) {
                vm.rv.InsuranceProfile = {
                    FullName: null,
                    DateOfBirth: null,
                    DriverLicenseNumber: null,
                    CountryOfIssue: null,
                    ProvinceStateOfIssue: null,
                    ClassOfLicense: null,
                    HadMoreThanEightYearFullDriverLicense: null,

                    HadMoreThanThreeFaultAccidentsLastSixYears: null,
                    DidHaveAnySeriousConvictions: null,
                    HadMoreThanThreeMinorDrivingConvictionsLastThreeYears: null,
                    HadMoreThanThreePersonalAutomobileInsuranceBeenCanceled: null,

                    FrontPicOfDriverLicense: null,
                    BackPicOfDriverLicense: null,
                    CurrentInsurancePolicyInPlace: null,
                    Provider: vm.rv.InsuranceProvide,
                    MakeOfRV: vm.rv.Make,
                    ModelOfRV: vm.rv.Model,
                    TypeOfRV: vm.rv.RentalType,
                    YearOfManufacturer: vm.rv.Year,
                    VINNumber: null,
                    LicensePlate: null,
                    CurrentValueOfRV: null,
                    Length: vm.rv.Length,
                    FactoryBuiltAndCSAApproved: null
                }
            } else {
                vm.formControl.KeepUpdate = true;
                vm.formControl.ManufacturerMaintainenceGuidelines = true;
            }


            if (vm.rv.InsuranceProfile.DateOfBirth) {
                vm.rv.InsuranceProfile.DateOfBirth = new Date(vm.rv.InsuranceProfile.DateOfBirth);
            }

        })

        // configs
        vm.rvSrv = SVCS.RVListing;
        // end configs

        // options
        vm.issuedCountries = ['Canada', 'USA', 'United Kingdom','European Union', 'Australia'];

        vm.getArrayNumbers = function (from, to) {
            var arr = [];
            for (var i = from; i <= to; i++) {
                arr.push(i);
            }
            return arr;
        }

        vm.typesOfRV = [
            { Value: $rootScope.$rentalType.TravelTrailer.Value, Display: $rootScope.$rentalType.TravelTrailer.TextCode },
            { Value: $rootScope.$rentalType.MotorHome.Value, Display: $rootScope.$rentalType.MotorHome.TextCode }]

        vm.years = [];
        for (var i = 1960; i <= new Date().getFullYear(); i++) {
            vm.years.push(i);
        }

        vm.dobPickerOptions = {
            datepickerMode : 'year'
        }
        
        // end options

        // form control

        vm.formControl = {
            KeepUpdate: null,
            ManufacturerMaintainenceGuidelines: null
        }

        vm.loading = {
            UploadFrontPic: false,
            UploadBackPic: false,
            IP: false
        }

        // end form control

        // custom errors

        vm.customErrors = {
            FrontDLPic: false,
            BackDLPick: false
        }

        // end custom errors

        // pending upload handler

        vm.pendingUpload = {
            Front: null,
            Back: null
        }

        vm.pendingFiles = {
            Front: null,
            Back: null
        }

        // end pending upload handler

        // form action

        vm.onFrontDLPictureSelected = function (files, controlGetter) {
            var uploadImgControl = controlGetter();
            if (files != null && files.length) {
                vm.loading.UploadFrontPic = true;

                var reader = new FileReader();
                reader.readAsDataURL(files[0]);
                reader.onload = function () {

                    // because onload event is not in angular context then we need apply
                    $scope.$apply(function () {
                        vm.loading.UploadFrontPic = false;
                        vm.pendingUpload.Front = reader.result;
                        vm.pendingFiles.Front = files[0];
                    })
                };

                reader.onerror = function (error) {

                    // because onerror event is not in angular context then we need apply
                    $scope.$apply(function () {
                        vm.loading.UploadFrontPic = false;
                        uploadImgControl.val('');

                        $translate('RV.EDIT.INSURANCE.ERRORS.CANNOT_PROCESS_IMAGE').then(function (msg) {
                            alert(msg);
                        })
                    })
                };
            }
        }

        vm.onBackDLPictureSelected = function (files, controlGetter) {
            var uploadImgControl = controlGetter();
            if (files != null && files.length) {
                vm.loading.UploadBackPic = true;
                var reader = new FileReader();
                reader.readAsDataURL(files[0]);
                reader.onload = function () {

                    // because onload event is not in angular context then we need apply
                    $scope.$apply(function () {
                        vm.loading.UploadBackPic = false;
                        vm.pendingUpload.Back = reader.result;
                        vm.pendingFiles.Back = files[0];
                    })
                };

                reader.onerror = function (error) {

                    // because onerror event is not in angular context then we need apply
                    $scope.$apply(function () {
                        vm.loading.UploadBackPic = false;
                        uploadImgControl.val('');

                        $translate('RV.EDIT.INSURANCE.ERRORS.CANNOT_PROCESS_IMAGE').then(function (msg) {
                            alert(msg);
                        })
                    })
                };
            }
        }

        vm.submitInsurance = function () {

            // form validation
            if (!vm.rv.InsuranceProfile.FrontPicOfDriverLicense && !vm.rv.InsuranceProfile.BackPicOfDriverLicense) {
                if (!vm.pendingFiles) {
                    vm.customErrors = {
                        FrontDLPic: true,
                        BackDLPic: true
                    }
                } else {
                    if (!vm.pendingFiles.Front) {
                        vm.customErrors.FrontDLPic = true;
                    } else {
                        vm.customErrors.FrontDLPic = false;
                    }
                    if (!vm.pendingFiles.Back) {
                        vm.customErrors.BackDLPic = true;
                    } else {
                        vm.customErrors.BackDLPic = false;
                    }
                }
            }

            
            if ($scope.insuranceForm.$invalid || vm.customErrors.FrontDLPic || vm.customErrors.BackDLPic) {
                angular.forEach($scope.insuranceForm.$error, function (field) {
                    angular.forEach(field, function (errorField) {
                        errorField.$setTouched();
                    })
                });
                return;
            }

            // end form validation

            vm.loading.IP = true;
            RVListingService.updateInsuranceProfile(vm.rv.Id, vm.rv.InsuranceProfile, vm.pendingFiles.Front, vm.pendingFiles.Back)
                .then(function (insurance) {

                    vm.loading.IP = false;
                    insurance.DateOfBirth = new Date(insurance.DateOfBirth);

                    vm.rv.InsuranceProfile = insurance;

                    vm.pendingUpload = {
                        Front: null,
                        Back: null
                    }

                    vm.pendingFiles = {
                        Front: null,
                        Back: null
                    }

                    // update insurance profile for original at master scope (from edit-rv.controller.js)
                    $scope.__insuranceUpdated(vm.rv.InsuranceProfile);

                    $state.go('app.edit-rv.success-commit-insurance');

                }, function (errors) {

                    vm.loading.IP = false;
                    if (errors && errors.Message) {
                        alert(errors.Message);
                    } else {
                        $translate('RV.EDIT.BUSY').then(function (msg) {
                            alert(msg);
                        })
                    }

                })

        }

        // end form action
    }

})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('EditRVController', EditRVController);

    /** @ngInject */
    function EditRVController($state, $stateParams, $scope, $rootScope, RVListingService, $q, $translate, stringhelper) {
        var vm = this;

        vm.stringhelper = stringhelper;

        $scope.loading = {
            processingRV: false
        }

        // flag tracking rv for this view is loaded
        var rvLoaded = false;

        // pending task wait for rv loaded, this will store tasks get a clone of rv from $scope.__getCloneRV and resolve when rv is loaded
        var pendingCloneRVTasks = [];

        $scope.rv = {
            Id: $stateParams.id
        }

        $scope.loading.processingRV = true;
        RVListingService.getMyRVAsync($scope.rv.Id).then(function (rv) {

            $scope.loading.processingRV = false;

            if (rv) {

                if (rv.HSStartDate) {
                    rv.HSStartDate = new Date(rv.HSStartDate);
                }

                if (rv.HSEndDate) {
                    rv.HSEndDate = new Date(rv.HSEndDate);
                }

                $scope.rv = rv;

                if (!$scope.rv.InsuranceProfile && $state.current.name == 'app.edit-rv.insurance'
                    && (($scope.rv.RentalType != $rootScope.$rentalType.MotorHome.Value && $scope.rv.RentalType != $rootScope.$rentalType.TravelTrailer.Value) || $scope.rv.Location.State != 'Ontario')) {
                    $state.go('app.edit-rv.basics');
                }

                // set rvLoaded flag and resolve pending get clone of rv tasks
                rvLoaded = true;

                angular.forEach(pendingCloneRVTasks, function (deferer) {
                    resolveCloneRV(deferer);
                })

            } else {
                // redirect to create rv state if rv is not created
                $state.go('app.rvlisting.step1');
            }

        }, function () {
            $scope.loading.processingRV = false;
            $state.go('app.rvlisting.step1');
        })

        // hack for redirect to basics edit when state change from any child state to this state app.edit-rv
        var eventDeregisterHandler = $rootScope.$on('$stateChangeSuccess', function (event, toState, toParams, fromState, fromParams) {

            if (toState.name == 'app.edit-rv' && fromState.name.startsWith('app.edit-rv.')) {
                $state.go('app.edit-rv.basics');
            }

        })

        $scope.$on('$destroy', function () {
            eventDeregisterHandler();
        })

        // redirect to basics edit when state is master state
        if ($state.current.name == 'app.edit-rv') {
            $state.go('app.edit-rv.basics');
        }


        // shared scope method

        $scope.__save = function (rv) {
            $scope.loading.processingRV = true;

            RVListingService.updateRV(rv).then(function (updatedRv) {

                if (updatedRv.HSStartDate) {
                    updatedRv.HSStartDate = new Date(updatedRv.HSStartDate);
                }

                if (updatedRv.HSEndDate) {
                    updatedRv.HSEndDate = new Date(updatedRv.HSEndDate);
                }

                $scope.rv = updatedRv;
                angular.merge(rv, $scope.rv);

                $scope.loading.processingRV = false;

                $translate('RV.EDIT.UPDATED').then(function (msg) {
                    alert(msg);
                })

            }, function (errors) {
                $scope.loading.processingRV = false;

                if (errors && errors.Message) {
                    alert(errors.Message);
                } else {
                    $translate('RV.EDIT.BUSY').then(function (msg) {
                        alert(msg);
                    })
                }
            });

        }

        $scope.__getCloneRV = function () {
            var deferer = $q.defer();

            if (rvLoaded) {
                resolveCloneRV(deferer);
            } else {
                pendingCloneRVTasks.push(deferer);
            }

            return deferer.promise;
        }

        $scope.__insuranceUpdated = function (insuranceProfile) {
            var cloneOfInsuranceProfile = angular.copy(insuranceProfile);
            $scope.rv.InsuranceProfile = cloneOfInsuranceProfile;
        }

        function resolveCloneRV(deferer) {
            deferer.resolve(angular.copy($scope.rv));
        }

        $scope.__getCloneRV().then(function (rv) {
            vm.rv = rv;
        })

        $scope.deleteRV = function() {
            RVListingService.deleteRV(vm.rv.Id).then(function (){
                $state.go('app.myrvs.list');

            }, function (errors) {

            });
        }
        
    }
})();
