(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('EditRVAvailabilityController', EditRVAvailabilityController);

    /** @ngInject */
    function EditRVAvailabilityController($state, $scope, RVListingService, myRVs, $timeout, myCalendar, $stateParams, $translate) {
        var vm = this;
        vm.currentLang = $stateParams.lang;

        // need to clone an rv to this view scope for edit
        // call shared method from master view (from edit-rv.controller.js)
        $scope.__getCloneRV().then(function (rv) {
            vm.rv = rv;
         
            initView();
        })

        vm.SaveRentalDays = function () {

            RVListingService.updateRV(vm.rv).then(function (updatedRv) {
                if (updatedRv) {
                    vm.rv = updatedRv;

                    vm.rv.MinimumRentalDay = updatedRv.MinimumRentalDay;

                    $scope.__save(vm.rv);
                }

            }, function (errors) {

            });

        }

        function initView() {

            vm.calEventsExt = {
                color: '#f00',
                textColor: 'yellow',
                events: []
            };

            /* remove event */
            vm.remove = function (id) {
                var tempArray = vm.calEventsExt.events;
                for (var i = 0; i < vm.calEventsExt.events.length; i++) {

                    if (tempArray[i].id == id) {
                        tempArray.splice(i, 1);
                    }
                }
                vm.calEventsExt.events = tempArray;
            };

            /* update on Drop */
            vm.updateCalendarByDrop = function (calendarModel) {
                myRVs.updateCalendar(calendarModel).then(
                    function success(response) {
                    },
                    function error(response) {

                    }
                )
            };

            /* update on Resize */
            vm.updateCalendarByResize = function (calendarModel) {
                myRVs.updateCalendar(calendarModel).then(
                    function success(response) {
                    },
                    function error(response) {

                    }
                )
            };

            /* add custom event*/
            vm.addEvent = function (id, startDate, endDate, allDay) {

                vm.calEventsExt.events.push({
                    id: id,
                    title: '',
                    start: new Date(startDate),
                    end: new Date(endDate),
                    className: ['custom-feed'],
                    allDay: true
                });
            };

            /* delete on eventClick */
            vm.deleteCalendar = function (id) {
                myRVs.deleteCalendar(id).then(
                    function success(response) {
                    },
                    function error(response) {

                    }
                )
            };

            vm.createCalendar = function (calendarModel) {
                myRVs.createCalendar(calendarModel).then(
                    function success(response) {
                    },
                    function error(response) {

                    }
                )
            };

            function guid() {
                function s4() {
                    return Math.floor((1 + Math.random()) * 0x10000)
                        .toString(16)
                        .substring(1);
                }
                return s4() + s4() + '-' + s4() + '-' + s4() + '-' +
                    s4() + '-' + s4() + s4() + s4();
            }

            var isFirstTime = true;

            vm.uiConfig = {
                defaultView: 'month',
                calendar: {
                    height: 465,
                    selectable: true,
                    selectConstraint: {
                        start: moment().subtract(1, 'days'),
                        end: moment().startOf('month').add(12, 'month')
                    },
                    selectHelper: true,
                    select: function (start, end) {
                        var id = guid();

                        vm.addEvent(id, start, end);

                        var calendarModel = {
                            Id: id,
                            Subject: 'Subject',
                            EventName: 'Unavalable',
                            StartDate: myCalendar.formatStartDate(start),
                            EndDate: myCalendar.formatEndDate(end),
                            OwnerId: vm.rv.OwnerId,
                            RVId: vm.rv.Id,
                            className: ['custom-feed']
                        };

                        vm.createCalendar(calendarModel);
                    },
                    editable: true,
                    header: {
                        left: 'prev',
                        center: 'title',
                        right: 'next'
                    },
                    eventClick: function (calEvent, jsEvent, view) {
                        $translate('RV.EDIT.AVAILABILITY.CONFIRM_DELETE').then(function (msg2) {
                            if (confirm(msg2)) {
                                if (calEvent.className[0] == 'custom-feed')
                                {
                                    vm.deleteCalendar(calEvent.id);
                                    vm.remove(calEvent.id);
                                }
                                else {
                                    $translate('RV.EDIT.AVAILABILITY.CANT_DELETE').then(function (msg) {
                                        alert(msg);
                                    })
                              
                                    revertFunc();
                                }
                            }
                        })
                    },
                    eventDrop: function (event, delta, revertFunc) {
                        $translate('RV.EDIT.AVAILABILITY.CONFIRM_DROP').then(function (msg) {
                            if (!confirm(msg)) {
                                revertFunc();
                            }
                            else {
                                var calendarModel = {
                                    Id: event.id,
                                    Subject: 'Subject',
                                    EventName: 'Unavalable',
                                    StartDate: event.start,
                                    EndDate: event.end,
                                    OwnerId: vm.rv.OwnerId,
                                    RVId: vm.rv.Id
                                };

                                vm.updateCalendarByDrop(calendarModel);
                            }
                        })
                    },
                    eventResize: function (event, delta, revertFunc) {
                        $translate('RV.EDIT.AVAILABILITY.CONFIRM_RESIZE').then(function (msg) {
                            if (!confirm(msg)) {
                                revertFunc();
                            }
                            else {
                                var calendarModel = {
                                    Id: event.id,
                                    Subject: 'Subject',
                                    EventName: 'Unavalable',
                                    StartDate: event.start,
                                    EndDate: event.end,
                                    OwnerId: vm.rv.OwnerId,
                                    RVId: vm.rv.Id
                                };

                                vm.updateCalendarByResize(calendarModel);
                            }
                        })
                    },
                    viewRender: function (view, element) {
                        vm.getCalendarByRV();
                    },
                    eventAfterRender: function (event, element) {
                        var td = $(element).parent();
                        var thead = td.parent().parent().parent().parent();
                        var dateTds = $(thead.find('tr')[0]).find('td');

                        var idx = td.index();
                        var colspan = td.attr('colspan');
                        if (!colspan) {
                            colspan = 1;
                        }

                        colspan = parseInt(colspan);

                        for (var i = idx; i < idx + colspan; i++) {
                            $(dateTds[i]).addClass('fc-evented');
                        }
                    }

                }
            };

            vm.getCalendarByRV = function () {

                myRVs.getCalendarByRV(vm.rv.Id).then(
                    function success(response) {
                        var calendar = response.data;
                        vm.calEventsExt.events.length = 0;

                        for (var i = 0; i < calendar.length; i++) {
                            var event = {
                                id: calendar[i].Id,
                                title: '',
                                start: calendar[i].StartDate,
                                end: moment(calendar[i].EndDate).add(1, 'days'),
                                allDay: true,
                                className: (calendar[i].BookingId !== null) ? ['custom-feed2'] : ['custom-feed']
                            };

                            vm.calEventsExt.events.push(event);
                        }

                    },
                    function error(response) {

                    }
                )
            }

            /* event sources array*/
            vm.eventSources = [vm.calEventsExt];
        }

    }

})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('RVListingController', RVListingController);

    /** @ngInject */
    function RVListingController($state, $scope, $rootScope, RVListingService, $window) {
        var vm = this;
        $window.document.title = "Create RV | RVezy";
        $scope.loading = {
            processingRV: false
        }

        $scope.resetRV = function () {
            $scope.rv = {
                RentalType: null,
                RVType: null,
                Make: null,
                Model: null,
                Year: null,
                Length: null,
                Guests: null,
                Slideouts: null,
                InsuranceProvide: null,
                Location: {
                    ParkName: null,
                    Street: null,
                    City: null,
                    State: null,
                    PostCode: null
                },
                RVName: null,
                RVDescription: null,
                InteriorFeatures: [],
                CourtesyRequests: [],
                ParkAmenities: [],
                Weight: null,
                HitchWeight: null,
                HitchSize: null,
                SecurityDeposit: null,
                HighSeasonNight: null,
                HighSeasonWeek: null,
                HighSeasonMonth: null,
                LowSeasonNight: null,
                LowSeasonWeek: null,
                LowSeasonMonth: null,
                IsRegisteredCollection: null,
                AccountName: null,
                HSStartDate: null,
                HSEndDate: null,
                IsChargeMileage: null,
                DailyKMAllowed: null,
                SurchargePerExtraKM: 0,
                IsGenerator: null,
                FreeHoursPerDay: null,
                ChargePerHourOver: 0,
                Photos: [],
                files: []
            }
        }
        
        $scope.resetRV();        

        vm.steps = [
            {
                step: 1,
                state: 'app.rvlisting.step1',
                name: "RVMANAGEMENT.BASICS",
                image: "app/images/step_1.png",
                image_active: "app/images/step_1.png"
            },
            {
                step: 2,
                state: 'app.rvlisting.step2',
                name: "RVMANAGEMENT.LOCATION",
                image: "app/images/step_2.png",
                image_active: "app/images/step_2.active.png"
            },
            {
                step: 3,
                state: 'app.rvlisting.step3',
                name: "RVMANAGEMENT.DETAILS",
                image: "app/images/step_3.png",
                image_active: "app/images/step_3.active.png"
            },
            {
                step: 4,
                state: 'app.rvlisting.step4',
                name: "RVMANAGEMENT.PRICING",
                image: "app/images/step_4.png",
                image_active: "app/images/step_4.active.png"
            },
            {
                step: 5,
                state: 'app.rvlisting.step5',
                name: "RVMANAGEMENT.PHOTOS",
                image: "app/images/step_5.png",
                image_active: "app/images/step_5.active.png"
            }
        ];

        vm.currentStep = null;

        for (var i = 0; i < vm.steps.length; i++) {
            if ($state.current.name === vm.steps[i].state) {
                vm.currentStep = vm.steps[i].step;
                break;
            }
        }

        $scope.loading.processingRV = true;
        RVListingService.getMyDraft().then(function (draft) {
            if (draft) {

                if (draft.HSStartDate) {
                    draft.HSStartDate = new Date(draft.HSStartDate);
                }

                if (draft.HSEndDate) {
                    draft.HSEndDate = new Date(draft.HSEndDate);
                }

                angular.merge($scope.rv, draft);
            }

            $scope.loading.processingRV = false;
            if ($scope.rv.Id) {
                // redirect to step 1 if current step is not specified
                if (vm.currentStep == null) {
                    $state.go('app.rvlisting.step1');
                }
            } else {
                if (vm.currentStep != 1) {
                    $state.go('app.rvlisting.step1');
                }
            }
        }, function () {
            $scope.loading.processingRV = false;
            $state.go('app.rvlisting.step1');
        })

        // shared method for other step set current entered step
        $scope.setCurrentStep = function (step) {
            vm.currentStep = step;
        }

        // hack for redirect to step 1 when state change from any step to this state app.rvlisting
        var eventDeregisterHandler = $rootScope.$on('$stateChangeSuccess', function (event, toState, toParams, fromState, fromParams) {
            
            if (toState.name == 'app.rvlisting' && fromState.name.startsWith('app.rvlisting.')) {
                $state.go('app.rvlisting.step1');
            }

        })

        $scope.$on('$destroy', function () {
            eventDeregisterHandler();
        })


    }
})();
