(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('ListAnRVStep5Controller', ListAnRVStep5Controller);

    /** @ngInject */
    function ListAnRVStep5Controller($state, $scope, $sce, RVListingService, SVCS, $translate) {
        var vm = this;

        vm.temp = {};

        $scope.setCurrentStep(5);

        // init view

        vm.SVCS = SVCS;

        vm.moreInfoPopover = '';

        $translate('RV.EDIT.PHOTOS.MORE_INFO_POPOVER').then(function (msg) {
            vm.moreInfoPopover = $sce.trustAsHtml(msg);
        })

        // end init view

        // on data change handlers

        vm.onOrderChanged = function (newIndex) {

            $scope.loading.processingRV = true;
            RVListingService.setPhotoOrder($scope.rv.Photos[newIndex].Id, newIndex).then(function () {

                $scope.loading.processingRV = false;
                angular.forEach($scope.rv.Photos, function (p, idx) {
                    p.Order = idx;
                })

            }, function (error) {

                $scope.loading.processingRV = false;
                if (error && error.Message) {
                    alert(error.Message);
                } else {
                    $translate('RV.EDIT.BUSY').then(function (msg) {
                        alert(msg);
                    })
                }

            })

        }

        // end data change handlers

        // step methods

        vm.removePhoto = function (photo, idx) {

            $scope.loading.processingRV = true;
            RVListingService.removePhoto(photo.Id).then(function () {

                $scope.loading.processingRV = false;
                $scope.rv.Photos.splice(idx, 1);
            }, function (error) {

                $scope.loading.processingRV = false;
                if (error && error.Message) {
                    alert(error.Message);
                } else {
                    $translate('RV.EDIT.BUSY').then(function (msg) {
                        alert(msg);
                    })
                }

            })

        }

        vm.uploadPhoto = function (files, invalidFiles) {

            if (invalidFiles.length) {
                $translate('RV.EDIT.PHOTOS.ERRORS.FILE_INVALID', { name: invalidFiles[0].name }).then(function (msg) {
                    alert(msg);
                })

                return;
            }

            if (!files || !files.length) {
                return;
            }
            
            $scope.loading.processingRV = true;

            sequentialUpload(files, 0, [], sequentialUploadCompleted);
        }

        function sequentialUploadCompleted(errorsFiles) {
            var filesNames = [];
            if (errorsFiles && errorsFiles.length) {
                angular.forEach(errorsFiles, function (item) {
                    filesNames.push(errorsFiles.file.name);
                })
            }

            if (filesNames.length) {
                $translate('RV.EDIT.PHOTOS.ERRORS.UPLOAD_ERRORS', { names: filesNames.join(', ') }).then(function (msg) {
                    alert(msg);
                })
            }
        }



        function sequentialUpload(files, i, errorsFiles, callback) {
            RVListingService.uploadPhoto($scope.rv.Id, files[i]).then(function (photo) {

                if (!$scope.rv.Photos) {
                    $scope.rv.Photos = [];
                }

                $scope.rv.Photos.push(photo);

            }, function (error) {
                errorsFiles.push({ file: files[i], error: error });

            }).then(function () {
                if (files.length === i + 1) {
                    $scope.loading.processingRV = false;
                    callback(errorsFiles);
                } else {
                    sequentialUpload(files, i + 1, errorsFiles, callback);
                }
            });
        }

        vm.completeDraft = function () {

            $scope.loading.processingRV = true;
            RVListingService.completeDraft().then(function () {

                $scope.loading.processingRV = false;
                $state.go('app.rvlisting.success')

            }, function (error) {

                $scope.loading.processingRV = false;
                if (error && error.Message) {
                    alert(error.Message);
                } else {
                    $translate('RV.EDIT.BUSY').then(function (msg) {
                        alert(msg);
                    })
                }

            })

        }

        // end step methods


        // step handlers

        vm.back = function () {
            $state.go('app.rvlisting.step4');
        }

        // end step handlers

    }

})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .controller('ListAnRVStep4Controller', ListAnRVStep4Controller);

    /** @ngInject */
    function ListAnRVStep4Controller($state, $scope, RVListingService) {
        var vm = this;

        $scope.setCurrentStep(4);
        
        /* options */

        vm.dateMonthPickerOptions = {
            maxMode: 'day',
            formatDayTitle: 'MMMM'
        }

        /* end options */

        // on data change handlers

        /* handle HS Date changed */
        $scope.$watch(function () {
            return $scope.rv.HSStartDate
        }, function (val, old) {
            if (val != old) {
                vm.onHSStartDateChange();
            }
        })

        $scope.$watch(function () {
            return $scope.rv.HSEndDate
        }, function (val, old) {
            if (val != old) {
                vm.onHSEndaDateChange();
            }
        })

        vm.onHSStartDateChange = function () {
            if ($scope.rv.HSStartDate && $scope.rv.HSEndDate) {
                if ($scope.rv.HSStartDate > $scope.rv.HSEndDate) {
                    $scope.rv.HSEndDate = angular.copy($scope.rv.HSStartDate);
                }
            }
        }

        vm.onHSEndaDateChange = function () {
            if ($scope.rv.HSStartDate && $scope.rv.HSEndDate) {
                if ($scope.rv.HSStartDate > $scope.rv.HSEndDate) {
                    $scope.rv.HSStartDate = angular.copy($scope.rv.HSEndDate);
                }
            }
        }
        /* end handle HS Date changed */

        vm.onMileageChanged = function () {
            if (!$scope.rv.IsChargeMileage) {
                $scope.rv.DailyKMAllowed = null;
                $scope.rv.SurchargePerExtraKM = null;
            }
        }

        vm.onGeneratorChanged = function () {
            if (!$scope.rv.IsGenerator) {
                $scope.rv.FreeHoursPerDay = null;
                $scope.rv.ChargePerHourOver = null;
            }
        }

        vm.onFreeHoursPerDayChanged = function () {
            if ($scope.rv.FreeHoursPerDay != null && $scope.rv.FreeHoursPerDay != undefined) {
                if ($scope.rv.FreeHoursPerDay == 24) {
                    $scope.rv.ChargePerHourOver = null;
                }
            } else {
                $scope.rv.ChargePerHourOver = null;
            }
        }

        // end on data change handlers


        // step handlers

        vm.next = function () {
            if ($scope.pricingForm.$invalid) {

                angular.forEach($scope.pricingForm.$error, function (field) {
                    angular.forEach(field, function (errorField) {
                        errorField.$setTouched();
                    })
                });

                return;
            }

            $scope.loading.processingRV = true;
            RVListingService.saveDraft($scope.rv).then(function () {

                $scope.loading.processingRV = false;
                $state.go('app.rvlisting.step5');

            }, function () {

                $scope.loading.processingRV = false;

            })

        }

        vm.back = function () {

            $scope.loading.processingRV = true;
            RVListingService.saveDraft($scope.rv).then(function () {

                $scope.loading.processingRV = false;
                $state.go('app.rvlisting.step3');

            }, function () {

                $scope.loading.processingRV = false;

            })

        }

        // end step handlers

    }

})();
