(function () {
    'use strict';

    var serviceId = 'cms';

    angular.module('app.cms').factory(serviceId, cms);
    /** @ngInject */
    function cms($http, SVCS, $q) {

        var cmsServer = SVCS.CMS;

        function getAllTemplate() {
            return $http.get(cmsServer + '/api/cms/get-all-template');
        }

        function getTemplateByName(templateName) {
            var params = {
                templateName: templateName
            }
            return $http.get(cmsServer + '/api/cms/get-by-name', { params: params });
        }

        function getContentOfTemplate(templateId) {
            var params = {
                templateId: templateId
            }

            return $http.get(cmsServer + '/api/cms/get-content', { params: params });
        }

        function updateTemplate(templateModel) {
            var deferer = $q.defer();
            $http.put(cmsServer + '/api/cms', templateModel).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function updateContent(contentModel) {
            var deferer = $q.defer();
            $http.put(cmsServer + '/api/cms/update-content', contentModel).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        var service = {
            getAllTemplate: getAllTemplate,
            getTemplateByName: getTemplateByName,
            getContentOfTemplate: getContentOfTemplate,
            updateTemplate: updateTemplate,
            updateContent: updateContent
        };

        return service;
    }
})();
(function () {
    'use strict';
    var controllerId = 'cmsController'
    angular
        .module('app.cms')
        .directive('toggleAllCollapse', toggleAllCollapse)

    /** @ngInject **/
    function toggleAllCollapse() {
        return {
            restrict: 'A',
            link: function (scope, element, attrs) {

                var showTrigger = element.find('[show-all-collapse-trigger]');
                var hideTrigger = element.find('[hide-all-collapse-trigger]');
                showTrigger.click(function () {
                    element.find('.collapse').collapse('show');
                    showTrigger.hide();
                    hideTrigger.show();
                })
                hideTrigger.click(function () {
                    element.find('.collapse').collapse('hide');
                    showTrigger.show();
                    hideTrigger.hide();
                })
            }
        }
    }
})();
(function () {
    'use strict';
    var controllerId = 'cmsController'
    angular
        .module('app.cms')
        .directive('cmsHtml', cmsHtmlDirective)

    /** @ngInject **/
    function cmsHtmlDirective($compile) {
        return {
            restrict: 'A',
            link: function (scope, element, attrs) {
                build();

                scope.$watch(function () {
                    return scope.$eval(attrs.cmsHtml);
                }, function (val, old) {
                    if (val != old) {
                        build();
                    }
                })

                function build() {
                    var html = $compile(scope.$eval(attrs.cmsHtml))(scope);
                    element.html('');
                    element.append(html);
                }

            }
        }
    }
})();
(function () {
    'use strict';
    var controllerId = 'staticPageController'
    angular
        .module('app.cms')
        .controller(controllerId, ['$scope', 'pageContent', staticPageController]);
      /** @ngInject */
    function staticPageController($scope, pageContent) {
        $scope.template = pageContent;

        $scope.html = '';
        for (var i = 0; i < $scope.template.Contents.length; i++) {
            $scope.html += $scope.template.Contents[i].Source;
        }
    };
})();
(function () {
    'use strict';
    var controllerId = 'cmsController'
    angular
        .module('app.cms')
        .controller(controllerId, ['$scope', 'cms', '$stateParams', '$state', cmsController]);
      /** @ngInject */
    function cmsController($scope, cms, $stateParams, $state, stringhelper) {
        var vm = this;
        vm.currentLang = $stateParams.lang;
        vm.currentState = $state.current.name;
        vm.stringhelper = stringhelper;

        $scope.tinymceModel = '';

        $scope.tinymceOptions = {
            selector: 'textarea',
            height: 500,
            theme: 'modern',
            plugins: [
                'advlist autolink lists link image charmap preview hr anchor pagebreak',
                'searchreplace wordcount visualblocks visualchars code fullscreen',
                'insertdatetime media nonbreaking save table contextmenu directionality',
                'emoticons template paste textcolor colorpicker textpattern imagetools toc'
            ],
            toolbar1: 'undo redo | insert | styleselect | bold italic | alignleft aligncenter alignright alignjustify | bullist numlist outdent indent | link image preview media | forecolor backcolor emoticons code',
            image_advtab: true,
            templates: [
                { title: 'Blog', url: '/app/admin/cms/template/blog-tpl.html' },
                { title: 'Home Page en', url: '/app/admin/cms/template/home-tpl-en.html' },
                { title: 'Home Page fr', url: '/app/admin/cms/template/home-tpl-fr.html' }
            ],
            content_css: [
                '/app/admin/cms/tinymce/cms.css'
            ],
            valid_elements: '*[*]'

        };

        $scope.selectedTemplate = null;

        var getAllTemplate = function () {
            cms.getAllTemplate().then(
                function success(response) {
                    $scope.templates = response.data;
                },
                function error(response) {

                }
            );
        }

        getAllTemplate();

        $scope.contents = [];

        var getContentOfTemplate = function (templateId) {
            for (var i = 0; i < $scope.templates.length; i++) {
                if ($scope.templates[i].TemplateId === templateId) {
                    $scope.contents = $scope.templates[i].Contents;
                }
            }
        }

        $scope.showContentInEditor = function () {
            for (var i = 0; i < $scope.templates.length; i++) {
                if ($scope.selectedTemplate === $scope.templates[i].TemplateId) {

                    for (var j = 0; j < $scope.templates[i].Contents.length; j++) {
                        $scope.tinymceModel += $scope.templates[i].Contents[j].Source;
                    }

                    $scope.template = $scope.templates[i];
                    getContentOfTemplate($scope.selectedTemplate);

                }
            }
        }

        $scope.updateTemplate = function () {

            for (var j = 0; j < $scope.templates[0].Contents.length; j++) {
                $scope.tinymceModel += $scope.templates[0].Contents[j].Source;
            }

            var templateModel = {
                TemplateId: $scope.template.TemplateId,
                TemplateName: $scope.template.TemplateName,
                Source: $scope.tinymceModel,
                CreatedByUserId: $scope.template.CreatedByUserId,
                EditedByUserId: $scope.template.EditedByUserId,
                DeletedByUserId: $scope.template.DeletedByUserId,
                LastModifiedByUserId: $scope.template.LastModifiedByUserId,
                Status: $scope.template.Status,
                CreationDate: $scope.template.CreationDate,
                ModifiedDate: $scope.template.ModifiedDate,
                Contents: $scope.contents
            };

            cms.updateTemplate(templateModel).then(
                function success(response) {
                    $scope.contents = response.data;
                    alert('Updated Template success!')
                },
                function error(response) {

                }
            );
        };

        $scope.updateContent = function (contentModel) {
            cms.updateContent(contentModel).then(
                function success(response) {
                    alert('Updated content success!');
                },
                function error(response) {

                }
            );
        };
    };
})();
(function () {
    'use strict';

    angular
        .module('app.rvlisting')
        .factory('RVListingService', RVListingService);

    /** @ngInject */
    function RVListingService(SVCS, $http, $q, uiGmapGoogleMapApi, Upload) {
        var rvlistingServer = SVCS.RVListing;

        var service = {
            getMyRVAsync: getMyRVAsync,
            getMyDraft: getMyDraft,
            saveDraft: saveDraft,
            completeDraft: completeDraft,
            updateRV: updateRV,
            create: createRV,
            publish: publish,
            unpublish: unpublish,
            uploadPhoto: uploadPhoto,
            setPhotoOrder: setPhotoOrder,
            removePhoto: removePhoto,
            updateInsuranceProfile: updateInsuranceProfile,
            getCoordinateFromAddressAsync: getCoordinateFromAddressAsync,
            getRVAsync: getRVAsync,
            deleteRV: deleteRV
        };

        return service;

        function getRVAsync(id) {
            var deferer = $q.defer();

            $http.get(rvlistingServer + '/api/rvlistings/' + id).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function getMyRVAsync(id) {
            var deferer = $q.defer();

            $http.get(rvlistingServer + '/api/rvlistings/my-rvs/' + id).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function getMyDraft() {
            var deferer = $q.defer();

            $http.get(rvlistingServer + '/api/rvlistings/my-draft').then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function saveDraft(draft) {
            if (draft.Id) {
                return updateRV(draft);
            } else {
                return createRV(draft);
            }
        }

        function completeDraft() {
            var deferer = $q.defer();

            $http.put(rvlistingServer + '/api/rvlistings/my-draft/complete').then(function () {
                deferer.resolve();
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function createRV(rvlistingModel) {
            var deferer = $q.defer();

            $http.post(rvlistingServer + '/api/rvlistings/create', rvlistingModel)
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function updateRV(rv) {
            var deferer = $q.defer();

            $http.put(rvlistingServer + '/api/rvlistings/' + rv.Id, rv)
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function publish(rvId) {
            var deferer = $q.defer();

            $http.put(rvlistingServer + '/api/rvlistings/' + rvId + '/publish')
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function unpublish(rvId) {
            var deferer = $q.defer();

            $http.put(rvlistingServer + '/api/rvlistings/' + rvId + '/unpublish')
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function uploadPhoto(rvId, file) {
            var deferer = $q.defer();
            
            Upload.upload({
                url: rvlistingServer + '/api/rvlistings/' + rvId + '/upload-photo',
                data: { file: file }
            }).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            }, function (evt) {
            });
            
            return deferer.promise;
        }

        function setPhotoOrder(photoId, order) {
            var deferer = $q.defer();

            $http.put(rvlistingServer + '/api/rvlistings/photos/' + photoId + '/order/' + order)
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function removePhoto(photoId) {
            var deferer = $q.defer();

            $http.delete(rvlistingServer + '/api/rvlistings/photos/' + photoId)
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function updateInsuranceProfile(rvId, insuranceProfile, frontPic, backPic) {
            var deferer = $q.defer();

            var form = new FormData();
            angular.forEach(insuranceProfile, function (v, k) {
                if (k === 'DateOfBirth') {
                    form.append(k, v.toISOString());
                } else {
                    form.append(k, v);
                }
            })

            if (frontPic) {
                form.append('FrontDLPic', frontPic);
            }

            if (backPic) {
                form.append('BackDLPic', backPic);
            }

            $http.put(rvlistingServer + '/api/rvlistings/' + rvId + '/insurance', form, {
                headers: { 'Content-Type': undefined },
                transformRequest: angular.identity
            }).then(function (response) {
                deferer.resolve(response.data)
            }, function (responseError) {
                deferer.reject(responseError.data)
            })

            return deferer.promise;

        }

        function getCoordinateFromAddressAsync(addressParts, deferer) {
            var newDef = false;
            if (!deferer) {
                deferer = $q.defer();
                newDef = true;
            }

            // get coordination from address of rv
            uiGmapGoogleMapApi.then(function () {
                var address = addressParts.join(', ');
                var geocoder = new google.maps.Geocoder;
                geocoder.geocode({ 'address': address }, function (results, status) {
                    if (status === google.maps.GeocoderStatus.OK) {
                        deferer.resolve({ lat: results[0].geometry.location.lat(), lng: results[0].geometry.location.lng() });
                    } else {
                        if (addressParts.length > 1) {
                            var cloneAddressParts = angular.copy(addressParts);
                            cloneAddressParts.shift();
                            getCoordinateFromAddressAsync(cloneAddressParts, deferer);
                        } else {
                            deferer.reject();
                        }
                    }
                });
            }, function () {
                deferer.reject();
            })

            if (newDef) {
                return deferer.promise;
            }
        }

        function deleteRV(rvId) {
            var deferer = $q.defer();

            $http.put(rvlistingServer + '/api/rvlistings/' + rvId + '/delete')
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }
    }
})();
