(function () {
    'use strict';

    angular
        .module('app.payment')
        .factory('bookingPaymentService', bookingPaymentService);

    /** @ngInject */
    function bookingPaymentService(SVCS, $http, $q) {
        var BookingServer = SVCS.Booking;

        var payToReserve =  function(booking, token) {
            var deferer = $q.defer();
            $http.post(SVCS.Booking + '/api/booking/pay-to-reserve', {
                Id: booking.Id,
                RenterId: booking.RenterId,
                OwnerId: booking.OwnerId,
                RVId: booking.RVId,
                StripeToken: token ? token.id : null,
                ProtectionLevel: booking.ProtectionLevel
            }).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            });

            return deferer.promise;
        }

        return {
            payToReserve: payToReserve
        };
    }
})();
(function () {
    'use strict';

    angular
        .module('app.payment')
        .controller('BookingPayoutController', BookingPayoutController);

    function BookingPayoutController($scope, RVezyCfg, $timeout, bookingService, $state, $stateParams, bookingFeeCalculationService, bookingPaymentService, serviceFeeOptionsPromise, myBillingInfoService) {
        var vm = this;
        vm.loading = true;
        vm.mountStripeFieldsCompleted = false;
        vm.bookingCalculationCompleted = false;
        $scope.paymentFormLoad = true;

        $scope.clickNo = function () {
            $scope.paymentFormLoad = !$scope.paymentFormLoad;
        };

        /**
         * Set loading state to false if all part of the form is ready
         */
        function resetLoadingState() {
            vm.loading = !(vm.mountStripeFieldsCompleted && vm.bookingCalculationCompleted);
        }

        serviceFeeOptionsPromise.then(function (options) {
            $scope.ServiceFeeOptions = options;

            bookingService.getMyBooking($stateParams.bookingId).then(function (booking) {
                vm.Booking = booking;

                if (vm.Booking.Stage !== 'AwaitingPayment') {
                    $state.go('app.mybooking.listOwner');
                }

                //Parse data to vm.Booking
                parseData(vm.Booking, booking);

                bookingFeeCalculationService.InitializeFeeCalculationObject(vm.Booking, $scope.ServiceFeeOptions).then(function (res) {

                    vm.FeeCalculation = res;

                    bookingFeeCalculationService.CalcFee(vm.FeeCalculation);

                    vm.bookingCalculationCompleted = true;

                    resetLoadingState();});
                
            });
        })
        
        vm.changeProtectionLevel = function (level) {
            vm.Booking.ProtectionLevel = level;
            vm.FeeCalculation.ProtectionLevel = vm.Booking.ProtectionLevel;
            bookingFeeCalculationService.CalcFee(vm.FeeCalculation);
        }

        var stripe = Stripe(RVezyCfg.Stripe.PublishableKey);
        var cardNumberElement;

        $scope.cardErrorMsg = {};
        $scope.mountStripeFields = function () {
            var elements = stripe.elements();

            // Mount card fields
            cardNumberElement = elements.create('cardNumber', {});
            cardNumberElement.mount('#card-number-element');
            var cardExpiryElement = elements.create('cardExpiry', {});
            cardExpiryElement.mount('#card-expiry-element');
            var cardCvcElement = elements.create('cardCvc', {});
            cardCvcElement.mount('#card-cvc-element');
            var postalCodeElement = elements.create('postalCode', {});
            postalCodeElement.mount('#postal-code-element');

            cardNumberElement.on('change', function (event) {
                $scope.cardErrorMsg.All = '';
                if (event.error) {
                    $scope.cardErrorMsg.Number = event.error.message;
                } else {
                    $scope.cardErrorMsg.Number = '';
                }
                $scope.$apply();
            });

            cardExpiryElement.on('change', function (event) {
                $scope.cardErrorMsg.All = '';
                if (event.error) {
                    $scope.cardErrorMsg.Expiry = event.error.message;
                } else {
                    $scope.cardErrorMsg.Expiry = '';
                }
                $scope.$apply();
            });

            cardCvcElement.on('change', function (event) {
                $scope.cardErrorMsg.All = '';
                if (event.error) {
                    $scope.cardErrorMsg.Cvc = event.error.message;
                } else {
                    $scope.cardErrorMsg.Cvc = '';
                }
                $scope.$apply();
            });

            postalCodeElement.on('change', function (event) {
                $scope.cardErrorMsg.All = '';
                if (event.error) {
                    $scope.cardErrorMsg.Postal = event.error.message;
                } else {
                    $scope.cardErrorMsg.Postal = '';
                }
                $scope.$apply();
            });

            vm.mountStripeFieldsCompleted = true;
            resetLoadingState();
        }

        function onSubmitSuccess() {
            // To Congratulation page
            $state.go('app.payment.success');
        }

        $scope.paytoReserveWithCustomerId = function () {
            bookingPaymentService.payToReserve(vm.Booking).then(function (res) {
                console.log("charge 1");
            }, function (error) {

                });
            bookingPaymentService.payToReserve(vm.Booking).then(function (res) {
                console.log("charge 2");
            }, function (error) {

            });
        }

        $scope.submit = function (event) {
            $scope.cardErrorMsg.All = '';
            stripe.createToken(cardNumberElement).then(function (result) {
                if (result.token) {
                    bookingPaymentService.payToReserve(vm.Booking, result.token)
                        .then(onSubmitSuccess);
                } else if (result.error) {
                    $scope.cardErrorMsg.All = result.error.message;
                }
                $scope.$apply();
            });
        }

        myBillingInfoService.getBillingInfoMe().then(function (res) {
            if (res.CustomerId && res.Status == "Active")
            {
                $scope.stripeCustomerActive = true;
            }
        }, function (error) {
        });

        var parseData = function (result, booking) {

            result.HSEndDate = booking.BookingPricingSnapshot.HSEndDate;
            result.HSStartDate = booking.BookingPricingSnapshot.HSStartDate;
            result.HighSeasonNight = booking.BookingPricingSnapshot.HighSeasonNight;
            result.HighSeasonWeek = booking.BookingPricingSnapshot.HighSeasonWeek;
            result.HighSeasonMonth = booking.BookingPricingSnapshot.HighSeasonMonth;
            result.LowSeasonNight = booking.BookingPricingSnapshot.LowSeasonNight;
            result.LowSeasonWeek = booking.BookingPricingSnapshot.LowSeasonWeek;
            result.LowSeasonMonth = booking.BookingPricingSnapshot.LowSeasonMonth;
            result.RoadsideAssistanceState = booking.BookingPricingSnapshot.RoadsideAssistanceState;
            result.PlaceOfResidence = booking.BookingPricingSnapshot.PlaceOfResidence;
            result.IsChargeMileage = booking.BookingPricingSnapshot.IsChargeMileage;
            result.DailyKMAllowed = booking.BookingPricingSnapshot.DailyKMAllowed;
            result.SurchargePerExtraKM = booking.BookingPricingSnapshot.SurchargePerExtraKM;
            result.IsGenerator = booking.BookingPricingSnapshot.IsGenerator;
            result.FreeHoursPerDay = booking.BookingPricingSnapshot.FreeHoursPerDay;
            result.ChargePerHourOver = booking.BookingPricingSnapshot.ChargePerHourOver;
            result.StateOfRvLocation = booking.BookingPricingSnapshot.StateOfRvLocation;
            result.SecurityDeposit = booking.BookingPricingSnapshot.SecurityDeposit;
        }
    }
})();
