(function () {
    'use strict';

    var serviceId = 'myRVs';

    angular.module('app.myrvs').factory(serviceId,
        ['$http','SVCS','$q', myRVs]);

    function myRVs($http, SVCS, $q) {

        function getMyRVs() {
            return $http.get(SVCS.RVListing + '/api/rvlistings/myrvs');
        }

        function getCalendarByRV(rvId) {
            return $http.get(SVCS.Calendar + '/api/calendars/get-by-rv/' + rvId)
        }

        function deleteCalendar(id) {
            var deferer = $q.defer();
            var bookingId = null;

            $http.delete(SVCS.Calendar + '/api/calendars/' + id + "/" + bookingId)
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function updateCalendar(calendarModel) {
            var deferer = $q.defer();
            $http.put(SVCS.Calendar + '/api/calendars', calendarModel).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function createCalendar(calendarModel) {
            var deferer = $q.defer();
            $http.post(SVCS.Calendar + '/api/calendars/create', calendarModel).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        var service = {
            getMyRVs: getMyRVs,
            getCalendarByRV: getCalendarByRV,
            deleteCalendar: deleteCalendar,
            updateCalendar: updateCalendar,
            createCalendar: createCalendar
        };

        return service;
    }
})();
