(function () {
    'use strict';

    var serviceId = 'myCalendar';

    angular.module('app.mycalendar').factory(serviceId,
        ['$http','SVCS', myCalendar]);

    function myCalendar($http, SVCS) {

        function getMyRVs() {
            return $http.get(SVCS.RVListing + '/api/rvlistings/getmyrvs');
        }

        function getCalendarByRV(rvId) {
            return $http.get(SVCS.Calendar + '/api/calendars/get-by-rv/' + rvId)
        }

        function formatStartDate(startDate) {
            return startDate.add(5, 'hours');
        }

        function formatEndDate(endDate) {
            return endDate.subtract(1, 'day').add(5, 'hours').subtract(1, 'seconds');
        }

        var service = {
            getMyRVs: getMyRVs,
            getCalendarByRV: getCalendarByRV,
            formatStartDate: formatStartDate,
            formatEndDate: formatEndDate
        };

        return service;
    }
})();
