(function () {
    'use strict';

    angular
        .module('app.booking')
        .factory('myBookingService', myBookingService);

    /** @ngInject */
    function myBookingService(SVCS, $http, $q, $rootScope, $filter, bookingStage) {
        var authServer = SVCS.Auth;
        var profileServer = SVCS.Profile;
        var BookingServer = SVCS.Booking;
        var rvlistingServer = SVCS.RVListing;
        var notificationServer = SVCS.Notification;

        var BookList = [];
        var BookingViewModel = {
            RenterId: 0,
            OwnerId: 0,
            RVId: 0,
            Adults: 0,
            Children: 0,
            Pets: 0,
            RoadsideAssistanceState: false,
            ShortIntroduce: "",
            DateStart: "",
            DateEnd: ""
        }

        var getListBooking = function (bookingIndex, pageSize, filter) {
            var deferer = $q.defer();

            $http.get(BookingServer + '/api/booking/list-renter-booking', { params: { pagingIndex: bookingIndex, pageSize: pageSize, filter: filter } })
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    deferer.reject(responseError.data);

                }
            return deferer.promise;
        };

        var getListBookingOwner = function (bookingIndex, pageSize, filter) {
            var deferer = $q.defer();

            $http.get(BookingServer + '/api/booking/list-owner-booking', { params: { pagingIndex: bookingIndex, pageSize: pageSize, filter: filter } })
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    deferer.reject(responseError.data);
                }
            return deferer.promise;
        };

        var getListShortRV = function (RVIds) {
            var deferer = $q.defer();

            $http.get(rvlistingServer + '/api/rvlistings/list-short-rv', { params: { RVIds: RVIds } })
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    deferer.reject(responseError.data);
                }
            return deferer.promise;
        };

        var acceptBooking = function (idBooking) {
            var deferer = $q.defer();
            $http.post(BookingServer + '/api/booking/accept?idBooking=' + idBooking)
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    deferer.reject(responseError.data);
                }
            return deferer.promise;
        }

        var declineBooking = function (idBooking) {
            var deferer = $q.defer();
            $http.post(BookingServer + '/api/booking/decline-from-owner?idBooking=' + idBooking)
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    deferer.reject(responseError.data);
                }
            return deferer.promise;
        }

        var declineBookingFromRenter = function (idBooking) {
            var deferer = $q.defer();
            $http.post(BookingServer + '/api/booking/decline-from-renter?idBooking=' + idBooking)
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    deferer.reject(responseError.data);
                }
            return deferer.promise;
        }

        var findRVInList = function (RVId, RVList) {
            var found = $filter('filter')(RVList, { RVId: RVId }, true);
            if (found.length) {
                return found;
            } else {
                return null;
            }
        }

        var getThreadId = function (receiver, RVId, bookingId) {
            var deferer = $q.defer();

            $http.get(notificationServer + '/api/message/get-id-thread?receiver=' + receiver +
                '&RVId=' + RVId + '&BookingId=' + bookingId)
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    deferer.reject(responseError.data);
                }
            return deferer.promise;
        }

        function createCalendar(calendarModel) {
            var deferer = $q.defer();
            $http.post(SVCS.Calendar + '/api/calendars/create', calendarModel).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function getOwnedBookingAsync(bookingId) {
            var deferer = $q.defer();

            $http.get(SVCS.Booking + '/api/booking/' + bookingId).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function addOwnerCloseoutReview(bookingId, extraCharges, review) {
            var deferer = $q.defer();

            var model = {
                BookingId: bookingId,
                Review: review,
                CloseoutExtraCharges: extraCharges
            };

            $http.post(SVCS.Booking + '/api/booking/owner-closeout-review', model).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function addRenterCloseoutReview(bookingId, review) {
            var deferer = $q.defer();

            var model = {
                BookingId: bookingId,
                Review: review
            };

            $http.post(SVCS.Booking + '/api/booking/renter-closeout-review', model).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function openDispute(dispute) {
            var deferer = $q.defer();
            
            $http.post(SVCS.Booking + '/api/booking/open-dispute', dispute).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }
        
        function isBookingValidToAddOrRemoveDrivers(booking) {
            var notAllowedStages = [
                bookingStage.DeclinedByTheOwner.Value,
                bookingStage.DeclinedByTheRenter.Value,
                bookingStage.CanceledByTheRenter.Value,
                bookingStage.CanceledByTheOwner.Value,
                bookingStage.PendingOwnerCloseout.Value,
                bookingStage.PendingRenterCloseout.Value,
                bookingStage.PendingDispute.Value,
                bookingStage.PendingPayout.Value,
                bookingStage.Completed.Value
            ];

            var now = new Date();
            var dateStart = new Date(booking.DateStart);
            if (dateStart < now) {
                return false;
            }

            if (notAllowedStages.indexOf(booking.Stage) !== -1) {
                return false;
            }

            return true;
        }

        return {
            getListBooking: getListBooking,
            getListBookingOwner: getListBookingOwner,
            getListShortRV: getListShortRV,
            acceptBooking: acceptBooking,
            declineBooking: declineBooking,
            declineBookingFromRenter: declineBookingFromRenter,
            findRVInList: findRVInList,
            getThreadId: getThreadId,
            createCalendar: createCalendar,
            getOwnedBookingAsync: getOwnedBookingAsync,
            addOwnerCloseoutReview: addOwnerCloseoutReview,
            addRenterCloseoutReview: addRenterCloseoutReview,
            openDispute: openDispute,
            isBookingValidToAddOrRemoveDrivers: isBookingValidToAddOrRemoveDrivers
        };
    }
})();
