(function () {
    'use strict';

    angular
        .module('app.myaccount')
        .factory('myAccountService', myAccountService);

    /** @ngInject */
    function myAccountService(SVCS, $http , $q, $rootScope) {
        var authServer = SVCS.Auth;
        var profileServer = SVCS.Profile;
        var BookingServer = SVCS.Booking;
        var rvlistingServer = SVCS.RVListing;
        var notificationServer = SVCS.Notification;

        var getMyRVs = function () {
            var deferer = $q.defer();

            $http.get(rvlistingServer + '/api/rvlistings/myrvs')
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {

                    deferer.reject(responseError.data);

                }
            return deferer.promise;
        };

        var getOtherRVs = function (profileId) {
            var deferer = $q.defer();

            $http.get(rvlistingServer + '/api/rvlistings/list-rv-of-owner', { params: { profileId: profileId}})
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {

                    deferer.reject(responseError.data);

                }
            return deferer.promise;
        };

        var getProfileMe = function () {
            var deferer = $q.defer();

            $http.get(profileServer + '/api/user-profiles/me')
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    
                    deferer.reject(responseError.data);

                }
            return deferer.promise;
        };

        var getOtherProfile = function (profileId) {
            var deferer = $q.defer();

            $http.get(profileServer + '/api/user-profiles/profile', { params: { profileId: profileId } })
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {

                    deferer.reject(responseError.data);

                }
            return deferer.promise;
        };

        var uploadPhoto = function(file) {
            var deferer = $q.defer();

            var formData = new FormData();

            var f = new File([file], file.name)
            formData.append("file", f);

            $http.post(profileServer + '/api/user-profiles/me/upload-cover-image', formData, {
                transformRequest: angular.identity,
                headers: { 'Content-Type': undefined }
            }).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        var uploadAvatar = function (file) {
            var deferer = $q.defer();

            var formData = new FormData();

            var f = new File([file], file.name)
            formData.append("file", f);

            $http.put(profileServer + '/api/user-profiles/me/avatar', formData, {
                transformRequest: angular.identity,
                headers: { 'Content-Type': undefined }
            }).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        var removePhoto = function() {
            var deferer = $q.defer();
            
            $http.delete(profileServer + '/api/user-profiles/me/delete-cover-image')
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        var updateProfile = function (myAccountBindingModel) {
            var deferer = $q.defer();

            $http.put(profileServer + '/api/user-profiles/me', myAccountBindingModel)
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        };

        var changePassword = function (myPasswordModel) {
            var deferer = $q.defer();

            $http.put(authServer + '/api/account/me/password', myPasswordModel)
                .then(function () {
                    deferer.resolve();
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        return {
            getProfileMe: getProfileMe,
            uploadPhoto: uploadPhoto,
            removePhoto: removePhoto,
            updateProfile: updateProfile,
            uploadAvatar: uploadAvatar,
            getMyRVs: getMyRVs,
            changePassword: changePassword,
            getOtherProfile: getOtherProfile,
            getOtherRVs: getOtherRVs
        };
    }
})();
