(function () {
    'use strict';

    angular
        .module('app.mybillinginfo')
        .controller('MyBillingInfoController', MyBillingInfoController);

    /** @ngInject */
    function MyBillingInfoController($scope, $rootScope, RVezyCfg, myBillingInfoService, $window, $stateParams, $location) {
        var vm = this;
        $window.document.title = "My payout info | RVezy";

        $scope.getClass = function (path) {
            if ($stateParams.lang === 'en') {
                path = '/en' + path;

            }
            else {
                path = '/fr' + path;
            }

            return ($location.path().substr(0, path.length) === path) ? 'active' : '';
        }

        vm.customer = null;
        vm.isFormDisplayed = false;
        $scope.cardErrorMsg = {};
        vm.connectionCheck = false;
        $scope.tabActive = 0;

        $scope.click = function () {
            $scope.tabActive = $scope.tabActive == 0 ? 1 : 0;
        }

        var checkConnect = myBillingInfoService.checkStripeConnect();
        checkConnect.then(function (res) {
            if (res)
            {
                vm.connectionCheck = true;
                vm.accountStripeInfo = res;
            }
                
            else
                vm.connectionCheck = false;
        });

        var stripe = Stripe(RVezyCfg.Stripe.PublishableKey);
        var cardNumberElement;
        
        vm.displayBillingInfo = function (customer) {
            vm.customer = customer;
        };

        vm.displayForm = function () {
            vm.isFormDisplayed = true;
        }

        vm.mountStripeFields = function () {
            var elements = stripe.elements();

            // Mount card fields
            cardNumberElement = elements.create('cardNumber', {});
            cardNumberElement.mount('#card-number-element');
            var cardExpiryElement = elements.create('cardExpiry', {});
            cardExpiryElement.mount('#card-expiry-element');
            var cardCvcElement = elements.create('cardCvc', {});
            cardCvcElement.mount('#card-cvc-element');
            var postalCodeElement = elements.create('postalCode', {});
            postalCodeElement.mount('#postal-code-element');

            cardNumberElement.on('change', function (event) {
                $scope.cardErrorMsg.All = '';
                if (event.error) {
                    $scope.cardErrorMsg.Number = event.error.message;
                } else {
                    $scope.cardErrorMsg.Number = '';
                }
                $scope.$apply();
            });

            cardExpiryElement.on('change', function (event) {
                $scope.cardErrorMsg.All = '';
                if (event.error) {
                    $scope.cardErrorMsg.Expiry = event.error.message;
                } else {
                    $scope.cardErrorMsg.Expiry = '';
                }
                $scope.$apply();
            });

            cardCvcElement.on('change', function (event) {
                $scope.cardErrorMsg.All = '';
                if (event.error) {
                    $scope.cardErrorMsg.Cvc = event.error.message;
                } else {
                    $scope.cardErrorMsg.Cvc = '';
                }
                $scope.$apply();
            });

            postalCodeElement.on('change', function (event) {
                $scope.cardErrorMsg.All = '';
                if (event.error) {
                    $scope.cardErrorMsg.Postal = event.error.message;
                } else {
                    $scope.cardErrorMsg.Postal = '';
                }
                $scope.$apply();
            });

            vm.mountStripeFieldsCompleted = true;
        }
        vm.mountStripeFields();

        vm.submit = function (event) {
            stripe.createToken(cardNumberElement).then(function (result) {
                $scope.$apply(function () {
                    if (result.token) {
                        var userId = $rootScope.Account.Id;
                        myBillingInfoService.updateBillingInfoMe(userId, result.token.id).then(vm.submitSuccess, vm.submitError);
                    } else if (result.error) {
                        $scope.cardErrorMsg.All = result.error.message;
                    }
                });
              
            });
        }

        myBillingInfoService.getBillingInfoMe()
            .then(vm.displayBillingInfo, function (reason) {
            });

        vm.submitSuccess = function (customer) {
            vm.customer = customer;
            $scope.cardErrorMsg.All = "";
            vm.isFormDisplayed = false;
        }

        vm.submitError = function (error) {
            $scope.cardErrorMsg.All = error.Message;
            scrollTo(0, 0);
        }

        vm.registerConnect = function () {
            $window.location.href = 'https://connect.stripe.com/oauth/authorize?'
                + 'response_type=code'
                + '&client_id=' + RVezyCfg.Stripe.ConnectClientId
                + '&scope=read_write'
                + '&state=' + $rootScope.Account.Id;
        }
    }

})();
