(function () {
    'use strict';

    angular
        .module('fGoogleOAuth')
        .provider('$googleOAuth', GoogleOAuthProvider)

    function GoogleOAuthService(getGApiAsyncFn, $q) {

        var _isAuthorize = false;


        return {
            SignIn: function () {

                var deferer = $q.defer();

                getGApiAsyncFn().then(function (_gapi) {

                    var gAuth = _gapi.auth2.getAuthInstance();

                    gAuth.signIn().then(function (response) {
                        deferer.resolve(response);
                    }, function (responseErrors) {
                        deferer.reject(responseErrors);
                    })

                }, function () {

                    deferer.reject({ CannotLoadLibrary: true });

                })

                return deferer.promise;
            },

            IsAuthorized: function () {
                return _isAuthorize;
            },

            GetUserInfo: function () {
                var deferer = $q.defer();

                getGApiAsyncFn().then(function (_gapi) {

                    var gAuth = _gapi.auth2.getAuthInstance();

                    deferer.resolve(gAuth.currentUser.get())

                }, function () {

                    deferer.reject({ CannotLoadLibrary: true });

                })

                return deferer.promise;
            }
        }

    }

    function GoogleOAuthProvider() {

        var $injector = angular.injector(['ng']);
        var $q = $injector.get('$q');

        var CLIENT_ID = null;
        var API_KEY = null;

        var deferers = [];

        var gApiLoadCompleted = false;
        var gApiLoadFailed = false;

        function getGApiAsync() {
            var deferer = $q.defer();

            if (gApiLoadCompleted) {
                deferer.resolve(gapi);
            } else if (gApiLoadFailed) {
                deferer.reject();
            } else {
                deferers.push(deferer);
            }

            return deferer.promise;
        }

        function onClientLibraryLoaded() {
            gapi.load('client', function () {

                gapi.client.init({
                    'apiKey': API_KEY,
                    'clientId': CLIENT_ID,
                    'scope': 'profile email'
                }).then(function () {
                    gApiLoadCompleted = true;
                    if (deferers && deferers.length) {
                        while (deferers.length) {
                            var deferer = deferers.pop();
                            deferer.resolve(gapi);
                        }
                    }
                }, function (errors) {
                    gApiLoadFailed = true;
                    while (deferers.length) {
                        var deferer = deferers.pop();
                        deferer.reject(gapi);
                    }
                })
            });
        }


        this.init = function (client_id, api_key) {
            if (!CLIENT_ID) {
                CLIENT_ID = client_id;
                API_KEY = api_key;

                var e = window.document.createElement("script");
                e.type = "text/javascript";
                e.async = true;
                e.defer = true;
                e.src = "https://apis.google.com/js/api.js";
                e.onload = function () {
                    e.onload = function () { };
                    onClientLibraryLoaded();
                }

                e.onreadystatechange = function () {
                    if (e.readyState === 'complete') {
                        e.onload();
                    }
                }

                var t = window.document.getElementsByTagName("script")[0];
                t.parentNode.insertBefore(e, t);
            }
        }

        this.$get = GoogleOAuthFactory;



        /** @ngInject **/
        function GoogleOAuthFactory() {

            return new GoogleOAuthService(getGApiAsync, $q);

        }
    }

})();
