(function () {
    'use strict';

    angular
        .module('app.contact')
        .factory('contactService', contactService);

    /** @ngInject */
    function contactService(SVCS, $http , $q) {
        var notificationService = SVCS.Notification;

        var sendEmail = function (subject, sendMessage, senderEmail, isCopy) {
            var deferer = $q.defer();

            $http.get(notificationService + '/api/Message/send-email/' + subject + "/" + sendMessage + "/" + senderEmail + "/" + isCopy)
                .then(function (response) {
                    
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError);
                });

            return deferer.promise;
        };

        return {
            sendEmail: sendEmail
        };
    }
})();
(function () {
    'use strict';
    var controllerId = 'contactController'
    angular
        .module('app.contact')
        .controller(controllerId, ['$scope','contactService', '$stateParams', '$state', contactController]);
      /** @ngInject */
    function contactController($scope, contactService, $stateParams, $state, stringhelper) {
        var vm = this;
        vm.currentLang = $stateParams.lang;
        vm.stringhelper = stringhelper;

        $scope.yourName = "";
        $scope.yourEmail = "";
        $scope.subject = "";
        $scope.message = "";
        $scope.isSendYourSelfCopy = false;
        
        $scope.sendEmail = function () {

            if ($scope.contactForm.$invalid) {

                angular.forEach($scope.contactForm.$error, function (field) {
                    angular.forEach(field, function (errorField) {
                        errorField.$setTouched();
                    })
                });

                return;
            }

            $scope.isSendEmailLoading = true;
            contactService.sendEmail($scope.subject, $scope.message, $scope.yourEmail, $scope.isSendYourSelfCopy).then(
                function success(response) {
                    $scope.isSuccess = true;
                    $scope.yourName = "";
                    $scope.yourEmail = "";
                    $scope.subject = "";
                    $scope.message = "";
                    $scope.isSendYourSelfCopy = false;
                    $scope.isSendEmailLoading = false;
                },
                function error(response) {
                    $scope.isSuccess = false;
                    $scope.isSendEmailLoading = false;
                }
            );
        }
    };
})();
