(function () {
    'use strict';

    angular
        .module('app.auth')
        .factory('UserService', UserService);

    function UserService(SVCS, $http, $q) {

        var authServer = SVCS.Auth;
        var profileServer = SVCS.Profile;

        var service = {
            Search: Search,
            Get: Get,
            Register: Register,
            Delete: Delete,
            UploadAvatar: UploadAvatar,
            UpdateProfile: UpdateProfile
        };

        return service;

        function Register(model) {
            var deferer = $q.defer();

            $http.post(authServer + '/api/account/register', model)
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function Search(criteria) {
            var deferer = $q.defer();

            $http.get(profileServer + '/api/recruiters/search', { params: criteria })
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function Get(id) {
            var deferer = $q.defer();

            $http.get(profileServer + '/api/recruiters/' + id)
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function Delete(id) {
            var deferer = $q.defer();

            $http.delete(authServer + '/api/account/recruiter/' + id)
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function UploadAvatar(img) {
            var deferer = $q.defer();

            var formData = new FormData();
            formData.append('file', img);

            $http.put(profileServer + '/api/recruiters/me/avatar', formData, {
                transformRequest: angular.identity,
                headers: { 'Content-Type': undefined }
            }).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }

        function UpdateProfile(profileModel) {
            var deferer = $q.defer();

            $http.put(profileServer + '/api/recruiters/me', profileModel).then(function (response) {
                deferer.resolve(response.data);
            }, function (responseError) {
                deferer.reject(responseError.data);
            })

            return deferer.promise;
        }
    }
})();
(function () {
    'use strict';

    angular
        .module('app.auth')
        .factory('AuthenticationService', AuthenticationService)
        .factory('HttpBearerTokenAuthorizationInterceptor', ['$cookies', function ($cookies) {
            return {
                'request': function (config) {
                    var accessToken = $cookies.get('access_token');
                    if (config.params && config.params.isExternalRequest) {
                        delete (config.params.isExternalRequest)
                    } else if (accessToken) {
                        config.headers.Authorization = 'bearer ' + accessToken;
                    }
                    return config;
                }
            }
        }]).config(['$httpProvider', function ($httpProvider) {
            $httpProvider.interceptors.push('HttpBearerTokenAuthorizationInterceptor');
        }]);


    /** @ngInject */
    function AuthenticationService(SVCS, $cookies, $http, $q, $interval, $rootScope) {

        var baseUrl = SVCS.Auth;

        var loadCurrentPromises = [];
        var loadCurrentProfilePromises = [];

        var service = {
            IsAuthenticated: false,
            AccessToken: null,
            Account: null,
            GetCurrentAsync: function () {
                var deferer = $q.defer();

                if (service.Account == null) {
                    loadCurrentPromises.push(deferer);
                } else {
                    deferer.resolve(service.Account);
                }

                return deferer.promise;
            },
            Permissions: [],
            SignInAsync: SignInAsync,
            ExternalSignInAsync: ExternalSignInAsync,
            ExternalRegisterAsync: ExternalRegisterAsync,
            SignOut: SignOut,
            HasPermissions: HasPermissions,
            ChangePasswordAsync: ChangePasswordAsync,
            GetProfileAsync: function () {
                var deferer = $q.defer();

                if (service.Profile == null) {
                    loadCurrentProfilePromises.push(deferer);
                } else {
                    deferer.resolve(service.Profile);
                }

                return deferer.promise;
            },
            Profile: null
        };

        $rootScope.$authSrv = service;
        $rootScope.Account = null;

        function SignInAsync(userName, password, remember) {
            var deferer = $q.defer();

            $http.post(baseUrl + '/token', { UserName: userName, Password: password }, {
                headers: {
                    'Content-Type': 'application/x-www-form-urlencoded'
                },
                noBigBox: true,
                transformRequest: function (obj) {
                    var str = [];
                    for (var p in obj)
                        str.push(encodeURIComponent(p) + "=" + encodeURIComponent(obj[p]));
                    return str.join("&");
                }
            }).then(function (response) {
                _authenticate(response.data, remember);
                _syncPermissions().then(function () {
                    deferer.resolve();
                    $rootScope.$emit('AUTHENTICATED');
                })

            }, function (error) {
                deferer.reject(error.data);
            })

            return deferer.promise;
        }

        function ExternalSignInAsync(token, provider, remember) {
            var deferer = $q.defer();

            $http.post(baseUrl + '/api/account/external-login', { AccessToken: token, Provider: provider }).then(function (response) {
                _authenticate(response.data, remember);
                _syncPermissions().then(function () {
                    deferer.resolve();
                    $rootScope.$emit('AUTHENTICATED');
                })

            }, function (error) {
                deferer.reject(error.data);
            })

            return deferer.promise;
        }

        function ExternalRegisterAsync(registerModel, remember) {
            var deferer = $q.defer();

            $http.post(baseUrl + '/api/account/external-register', registerModel).then(function (response) {
                _authenticate(response.data, remember);
                _syncPermissions().then(function () {
                    deferer.resolve();
                    $rootScope.$emit('AUTHENTICATED');
                })

            }, function (error) {
                deferer.reject(error.data);
            })

            return deferer.promise;
        }

        function ChangePasswordAsync(model) {
            var deferer = $q.defer();

            $http.put(baseUrl + '/api/account/me/password', model).then(function () {
                deferer.resolve();
            }, function (error) {
                deferer.reject(error.data);
            })

            return deferer.promise;
        }

        function SignOut() {
            $cookies.remove('access_token');
            $cookies.remove('account_id');
            service.IsAuthenticated = false;
            service.AccessToken = null;
            $rootScope.Account = service.Account = null;
            service.Profile = null
            service.Permissions = [];
        }

        function HasPermissions(permissions) {
            if (!service.IsAuthenticated) {
                return false;
            }
            else if (permissions && permissions.length) {
                if (!service.Permissions.length) {
                    return false;
                }
                for (var i = 0; i < permissions.length; i++) {
                    if (service.Permissions.indexOf(permissions[i]) != -1) {
                        return true;
                    }
                }
                return false;
            } else {
                return true;
            }
        }

        function _authenticate(signInResponse, remember) {
            var cookieOptions = {
                path: '/'
            };

            if (remember) {
                cookieOptions.expires = new Date(signInResponse.Expires);
            }

            $cookies.put('access_token', signInResponse.AccessToken, cookieOptions);
            $cookies.put('account_id', signInResponse.Account.Id, cookieOptions);
            service.AccessToken = signInResponse.AccessToken;
            $rootScope.Account = service.Account = signInResponse.Account;
            service.IsAuthenticated = true;
        }

        var firstCheck = true;
        function _checkAuthentication() {
            service.AccessToken = $cookies.get('access_token');
            service.IsAuthenticated = service.AccessToken && firstCheck ? true : service.IsAuthenticated;
            if (service.AccessToken) {
                $http.get(baseUrl + '/api/account/is-me-authenticated', { hideAjaxLoader: true }).then(function (authenticated) {
                    service.IsAuthenticated = authenticated.data;
                    if (service.IsAuthenticated) {
                        if (!service.Account) {
                            $http.get(baseUrl + '/api/account/me', { hideAjaxLoader: true }).then(function (account) {
                                $rootScope.Account = service.Account = account.data;
                                var deferer = loadCurrentPromises.pop();
                                while (deferer) {
                                    deferer.resolve(service.Account);
                                    deferer = loadCurrentPromises.pop();
                                }
                            })
                            _syncPermissions();
                        }
                    } else if (firstCheck) {
                        service.SignOut();
                        window.location.reload();
                    }
                    firstCheck = false;
                })
            }
        }

        function _syncPermissions() {
            if (service.IsAuthenticated) {
                return $http.get(baseUrl + '/api/account/me/permissions', { hideAjaxLoader: true }).then(function (permissions) {
                    service.Permissions = permissions.data;
                    $rootScope.$emit('PERMISSIONS_LOADED');

                    $http.get(SVCS.Profile + '/api/user-profiles/me').then(function (profile) {
                        service.Profile = profile.data;
                        var deferer = loadCurrentProfilePromises.pop();
                        while (deferer) {
                            deferer.resolve(service.Profile);
                            deferer = loadCurrentProfilePromises.pop();
                        }
                    })
                })
            } else {
                var deferer = $q.defer();
                deferer.reject();
                return deferer.promise;
            }
        }

        $interval(_checkAuthentication, 300000);
        _checkAuthentication();

        return service;
    }
})();
(function () {
    'use strict';

    angular
        .module('app.auth')
        .factory('RecoveryPasswordService', RecoveryPasswordService)

    /** @ngInject **/
    function RecoveryPasswordService($q, $http, SVCS) {

        return {

            RequestRecoveryCode: _requestRecoveryCode,
            ResetPassword: _resetPassword
        }

        function _requestRecoveryCode(model) {
            var deferer = $q.defer();

            $http.post(SVCS.Auth + '/api/forgot-password/request-code', model)
                .then(function () {
                    deferer.resolve();
                }, function (responseErrors) {
                    deferer.reject(responseErrors.data);
                })

            return deferer.promise;
        }

        function _resetPassword(model) {
            var deferer = $q.defer();

            $http.post(SVCS.Auth + '/api/forgot-password/reset-password', model)
                .then(function () {
                    deferer.resolve();
                }, function (responseErrors) {
                    deferer.reject(responseErrors.data);
                })

            return deferer.promise;
        }

    }

})();
(function () {
    'use strict';

    angular
        .module('app.auth')
        .controller('RecoveryPasswordController', RecoveryPasswordController)

    /** @ngInject **/
    function RecoveryPasswordController($scope, $stateParams, RecoveryPasswordService, $translate, $state, AuthenticationService, $timeout) {

        if (AuthenticationService.IsAuthenticated) {
            $state.go('app.home');
            return;
        }

        $scope.$watch(function () {
            return AuthenticationService.IsAuthenticated;
        }, function (val, old) {
            if (val != old && val) {
                $timeout(function () {
                    $state.go('app.home');
                }, 200)
            }
        })

        var vm = this;

        // handle form loading
        vm.processing = false;

        // model for request a recovery code
        vm.request = {
            Email: null
        };

        // model for reset password
        vm.reset = {
            Email: null,
            Code: null,
            Password: null,
            ConfirmPassword: null
        };

        // flag handle requesting recovery or recovery display
        vm.isRequesting = true;

        if ($stateParams.code) {
            _changeView(false);
        } else {
            vm.reset.Code = $stateParams.code;
            _changeView(true);
        }

        vm.ChangeView = _changeView;

        vm.RequestRecovery = _requestRecovery;

        vm.Recovery = _recovery;

        function _changeView(isRequesting) {
            if (isRequesting) {
                if (vm.reset.Email) {
                    vm.request.Email = vm.reset.Email;
                }
            } else {
                if (vm.request.Email) {
                    vm.reset.Email = vm.request.Email;
                }
                vm.reset.Password = null;
                vm.reset.ConfirmPassword = null;
                vm.reset.Code = $stateParams.code;
            }

            // set untouched to hide errors
            if ($scope.RequestRecoveryForm) {
                $scope.RequestRecoveryForm.Email.$touched = false;
            }

            // set untouched to hide errors
            if ($scope.RecoveryForm) {
                $scope.RecoveryForm.Email.$touched = false;
                $scope.RecoveryForm.Code.$touched = false;
                $scope.RecoveryForm.Password.$touched = false;
                $scope.RecoveryForm.ConfirmPassword.$touched = false;
            }

            vm.isRequesting = isRequesting;
        }


        function _requestRecovery() {
            if ($scope.RequestRecoveryForm.$invalid) {

                $scope.RequestRecoveryForm.Email.$touched = true;

                return;
            }

            // turn on form loading
            vm.processing = true;
            RecoveryPasswordService.RequestRecoveryCode(vm.request)
                .then(function () {
                    vm.processing = false;

                    $translate('AUTH.RECOVERY.CODE_SENT').then(function (msg) {
                        alert(msg);
                    })

                    _changeView(false);

                }, function (errors) {
                    // turn off form loading
                    vm.processing = false;

                    var msgCode = 'AUTH.RECOVERY.ERRORS.CANNOT_GET_CODE';
                    if (errors && errors.Code == 'EMAIL_NOT_FOUND') {
                        msgCode = 'AUTH.RECOVERY.ERRORS.EMAIL_NOT_FOUND';
                    }
                    $translate(msgCode).then(function (msg) {
                        alert(msg);
                    })
                })
        }

        function _recovery() {
            if ($scope.RecoveryForm.$invalid || vm.reset.Password != vm.reset.ConfirmPassword) {

                // set touched on fields to display errors
                $scope.RecoveryForm.Email.$touched = true;
                $scope.RecoveryForm.Code.$touched = true;
                $scope.RecoveryForm.Password.$touched = true;
                $scope.RecoveryForm.ConfirmPassword.$touched = true;

                return;
            }

            // turn on form loading
            vm.processing = true;
            RecoveryPasswordService.ResetPassword(vm.reset).then(function () {
                // turn off form loading
                vm.processing = false;

                $translate('AUTH.RECOVERY.PASSWORD_RESETTED').then(function (msg) {
                    alert(msg);
                })

                vm.request = {
                    Email: null
                };

                vm.reset = {
                    Email: null,
                    Code: null,
                    Password: null,
                    ConfirmPassword: null
                };

                // set untouched to hide errors
                $scope.RecoveryForm.Email.$touched = false;
                $scope.RecoveryForm.Code.$touched = false;
                $scope.RecoveryForm.Password.$touched = false;
                $scope.RecoveryForm.ConfirmPassword.$touched = false;

            }, function (errors) {
                // turn off form loading
                vm.processing = false;

                var msgCode = 'AUTH.RECOVERY.ERRORS.CANNOT_RESET';
                if (errors && errors.Code == 'INCORRECT_RECOVERY') {
                    msgCode = 'AUTH.RECOVERY.ERRORS.INCORRECT_RECOVERY';
                }
                $translate(msgCode).then(function (msg) {
                    alert(msg);
                })

            })
        }

    }

})();
(function () {
    'use strict';

    angular
        .module('app.admin', ['app.cms'])
        .config(config);

    /** @ngInject **/
    function config($stateProvider, $translatePartialLoaderProvider) {

        // states
        $stateProvider
            .state('app.admin', {
                url: '/admin',
                views: {
                    'main@': {
                        templateUrl: 'app/admin/layout/body.html'
                    },
                    'header@': {
                        controller: 'AdminHeaderController',
                        controllerAs: 'vm',
                        templateUrl: 'app/admin/layout/header.html'
                    }
                },
                abstract: true,
                protect: true,
                permissions: ['ADMINISTRATOR']
            })
            .state('app.admin.index', {
                url: '',
                templateUrl: 'app/admin/dashboard/index.html',
                controller: 'AdminDashboardController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['ADMINISTRATOR']
            })
            .state('app.admin.users', {
                url: '/users',
                templateUrl: 'app/admin/users/manage-users.html',
                controller: 'AdminManageUsersController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['MANAGE_ACCOUNTS']
            })
            .state('app.admin.roles', {
                url: '/roles',
                templateUrl: 'app/admin/roles/manage-roles.html',
                controller: 'AdminManageRolesController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['MANAGE_ACCOUNTS']
            })
            .state('app.admin.rvs', {
                url: '/rvs',
                templateUrl: 'app/admin/rvs/manage-rvs.html',
                controller: 'AdminManageRVsController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['ADMINISTRATOR']
            })
            .state('app.admin.bookings', {
                url: '/bookings',
                template: '<ui-view></ui-view>',
                protect: true,
                abstract: true,
                permissions: ['ADMINISTRATOR']
            })
            .state('app.admin.bookings.list', {
                url: '',
                templateUrl: 'app/admin/bookings/manage-bookings.html',
                controller: 'AdminManageBookingsController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['ADMINISTRATOR']
            })
            .state('app.admin.bookings.detail', {
                url: '/:id',
                templateUrl: 'app/admin/bookings/manage-detail-booking.html',
                controller: 'AdminManageDetailBookingController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['ADMINISTRATOR'],
                globalLoading: true,
                resolve: {
                    /** @ngInject **/
                    BookingInfo: function ($q, $stateParams, manageBookingsService, manageUsersService, manageRVsService) {
                        var deferer = $q.defer();

                        var bookingId = $stateParams.id;
                        var booking;
                        manageBookingsService.getBookingsAsync(bookingId).then(function (data) {
                            if (!data) {
                                deferer.reject();
                                alert('Booking #' + bookingId + ' cannot be found');
                            } else {

                                booking = data;
                                var profileIds = [booking.OwnerId, booking.RenterId];

                                manageUsersService.getUsersByIds(profileIds).then(function (users) {

                                    booking.Owner = users[booking.OwnerId];
                                    booking.Renter = users[booking.RenterId];

                                    manageRVsService.getRVAsync(booking.RVId).then(function (rv) {

                                        booking.RV = rv;
                                        deferer.resolve(booking);

                                    }, function () {
                                        deferer.reject();
                                        alert('Cannot load booking #' + bookingId + ' now. Please try later.');
                                    })

                                }, function () {
                                    deferer.reject();
                                    alert('Cannot load booking #' + bookingId + ' now. Please try later.');
                                })

                            }
                        }, function (errors) {
                            deferer.reject();
                            alert('Cannot load booking #' + bookingId + ' now. Please try later.');
                        })

                        return deferer.promise;
                    }
                }
            })
            .state('app.admin.reports', {
                url: '/reports',
                template: '<ui-view></ui-view>',
                abstract: true,
                protect: true,
                permissions: ['ADMINISTRATOR']
            })
            .state('app.admin.reports.users', {
                url: '/users',
                templateUrl: "app/admin/reports/users-report.html",
                controller: 'AdminUsersReportController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['ADMINISTRATOR']
            })
            .state('app.admin.reports.rvs', {
                url: '/rvs',
                templateUrl: "app/admin/reports/rvs-report.html",
                controller: 'AdminRVsReportController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['ADMINISTRATOR']
            })
            .state('app.admin.reports.finance', {
                url: '/finance',
                templateUrl: "app/admin/reports/finance-report.html",
                controller: 'AdminFinanceReportController',
                controllerAs: 'vm',
                protect: true,
                permissions: ['ADMINISTRATOR']
            })

        // Translation
        $translatePartialLoaderProvider.addPart('app/admin');

    }

})();
