(function () {
    'use strict';

    angular
        .module('app.admin')
        .factory('manageRolesService', manageRolesService);

    /** @ngInject **/
    function manageRolesService(SVCS, $http, $q) {

        return {
            getGroupsPermissionsAsync: getGroupsPermissionsAsync,
            getRolesAsync: getRolesAsync,
            getRolesPermissionsAsync: getRolesPermissionsAsync,
            createRoleAsync: createRoleAsync,
            updateRoleAsync: updateRoleAsync,
            deleteRoleAsync: deleteRoleAsync,
            setPermissionsAsync: setPermissionsAsync,
            removePermissionsAsync: removePermissionsAsync
        }

        function getGroupsPermissionsAsync() {
            var deferer = $q.defer();

            $http.get(SVCS.Auth + '/api/permissions/groups')
                .then(function (response) {
                    deferer.resolve(response.data)
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function getRolesAsync() {
            var deferer = $q.defer();

            $http.get(SVCS.Auth + '/api/roles')
                .then(function (response) {
                    deferer.resolve(response.data)
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function getRolesPermissionsAsync() {
            var deferer = $q.defer();

            $http.get(SVCS.Auth + '/api/roles/permissions')
                .then(function (response) {
                    deferer.resolve(response.data)
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function createRoleAsync(model) {
            var deferer = $q.defer();
            
            $http.post(SVCS.Auth + '/api/roles', model)
                .then(function (response) {
                    deferer.resolve(response.data)
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function updateRoleAsync(id, model) {
            var deferer = $q.defer();

            $http.put(SVCS.Auth + '/api/roles/' + id, model)
                .then(function (response) {
                    deferer.resolve(response.data)
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function deleteRoleAsync(id) {
            var deferer = $q.defer();

            $http.delete(SVCS.Auth + '/api/roles/' + id)
                .then(function () {
                    deferer.resolve()
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function setPermissionsAsync(roleId, permissions) {
            var deferer = $q.defer();

            $http.put(SVCS.Auth + '/api/roles/' + roleId + '/assign-permissions', { PermissionsIds: permissions })
                .then(function (response) {
                    deferer.resolve(response.data)
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        function removePermissionsAsync(roleId, permissions) {
            var deferer = $q.defer();

            $http.put(SVCS.Auth + '/api/roles/' + roleId + '/remove-permissions', { PermissionsIds: permissions })
                .then(function (response) {
                    deferer.resolve(response.data)
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

    }

})();
(function () {
    'use strict';

    angular
        .module('app.admin')
        .controller('AdminManageRolesController', adminManageRolesController);

    /** @ngInject **/
    function adminManageRolesController($scope, SVCS, manageRolesService, $uibModal, $translate) {
        var vm = this;

        $scope.SVCS = SVCS;

        // data for roles table
        vm.roles = [];

        // data for permissions assignment panel
        vm.groupsPermissions = [];

        // private variable for quick check permissions
        var _numberOfPermissions = null;
        var _allPermissionsCodes = [];

        // role instance to edit permissions
        vm.role = null;

        // handle loading and init failed flags
        vm.flags = {
            loadingRoles: false,
            loadRolesFailed: false,
            loadingPermissions: false,
            loadPermissionsFailed: false
        }

        // call first get to init table
        getRoles();

        // init permissions assignment panel
        getGroupsPermissions();

        vm.add = function () {
            var modalInstance = $uibModal.open({
                animation: true,
                ariaLabelledBy: 'modal-title',
                ariaDescribedBy: 'modal-body',
                templateUrl: 'app/admin/roles/manage-role.dialog.html',
                controller: 'ManageRoleDialogController',
                size: 'md',
                resolve: {
                    editingRole: null
                },
                appendTo: undefined
            });

            modalInstance.result.then(function (addedRole) {
                addedRole.Permissions = [];
                vm.roles.push(addedRole);

            }, function () {
                // empty function to prevent angular write error log about unhandle rejection of promise
                // this function is to handle modal dismiss (cancel) event
            });
        }

        vm.edit = function (role, idx) {

            if (vm.processing) {
                return;
            }

            var modalInstance = $uibModal.open({
                animation: true,
                ariaLabelledBy: 'modal-title',
                ariaDescribedBy: 'modal-body',
                templateUrl: 'app/admin/roles/manage-role.dialog.html',
                controller: 'ManageRoleDialogController',
                size: 'md',
                resolve: {
                    editingRole: role
                },
                appendTo: undefined
            });

            modalInstance.result.then(function (updatedRole) {

                vm.roles[idx] = updatedRole;

            }, function () {
                // empty function to prevent angular write error log about unhandle rejection of promise
                // this function is to handle modal dismiss (cancel) event
            });
        }

        vm.delete = function (role, idx) {
            $translate('ADMIN.ROLES.MESSAGES.DELETE_ROLE_CONFIRM_MSG').then(function (msg) {
                if (confirm(msg)) {

                    vm.flags.loadingRoles = true;
                    role.deleting = true;
                    manageRolesService.deleteRoleAsync(role.Id).then(function () {

                        vm.flags.loadingRoles = false;
                        vm.roles.splice(idx, 1);

                    }, function () {

                        role.deleting = false;
                        vm.flags.loadingRoles = false;

                        $translate('ADMIN.ROLES.ERRORS.CANNOT_DELETE_ROLE_NOW').then(function (errMsg) {
                            alert(errMsg);
                        })

                    })

                }
            })
        }

        // select a role to assign permissions
        vm.editPermissions = function (role) {
            vm.role = role;
        }

        /* handle permissions assignment fn */
        vm.isSelectAll = function () {
            return vm.role ? vm.role.Permissions.length === _numberOfPermissions : false;
        }

        
        vm.toggleAll = function () {
            var select = vm.isSelectAll();

            if (!select) {
                setPermissions(_allPermissionsCodes);
            } else {
                removePermissions(_allPermissionsCodes);
            }
        }

        vm.toggleGroup = function (group) {
            var permissionsSet = [];
            angular.forEach(group.Permissions, function (p) {
                permissionsSet.push(p.Id);
            })

            if (vm.isGroupChecked(group)) {
                removePermissions(permissionsSet);
            } else {
                setPermissions(permissionsSet);
            }
        }

        vm.isGroupChecked = function (group) {
            var check = true;

            for (var i = 0; i < group.Permissions.length; i++) {
                if (vm.role.Permissions.indexOf(group.Permissions[i].Id) < 0) {
                    check = false;
                    break;
                }
            }

            return check;
        }

        vm.togglePermission = function (permission) {
            var idx = vm.role.Permissions.indexOf(permission.Id);
            if (idx !== -1) {
                removePermissions([permission.Id]);
            } else {
                setPermissions([permission.Id]);
            }
        }

        vm.isPermissionChecked = function (permission) {
            return vm.role ? vm.role.Permissions.indexOf(permission.Id) !== -1 : false;
        }
        /* end handle permissions assignment */

        function getRoles() {
            vm.flags.loadRolesFailed = false;
            vm.flags.loadingRoles = true;

            manageRolesService.getRolesPermissionsAsync()
                .then(function (response) {
                    vm.flags.loadingRoles = false;

                    vm.roles = response;

                }, function (errors) {
                    vm.flags.loadingRoles = false;
                    vm.flags.loadRolesFailed = true;
                })
        }

        function getGroupsPermissions() {
            vm.flags.loadingPermissions = true;
            vm.flags.loadPermissionsFailed = false;

            manageRolesService.getGroupsPermissionsAsync().then(function (groupsPermissions) {

                vm.flags.loadingPermissions = false;

                vm.groupsPermissions = groupsPermissions;

                _numberOfPermissions = 0;

                angular.forEach(vm.groupsPermissions, function (g) {
                    angular.forEach(g.Permissions, function (p) {
                        _numberOfPermissions++;
                        _allPermissionsCodes.push(p.Id);
                    })
                })

            }, function () {
                vm.flags.loadingPermissions = false;
                vm.flags.loadPermissionsFailed = true;
            })
        }


        function setPermissions(permissions) {
            vm.flags.loadingPermissions = true;
            manageRolesService.setPermissionsAsync(vm.role.Id, permissions).then(function () {
                vm.flags.loadingPermissions = false;
                angular.forEach(permissions, function (p) {
                    var idx = vm.role.Permissions.indexOf(p);
                    if (idx < 0) {
                        vm.role.Permissions.push(p)
                    }
                })
            }, function () {
                vm.flags.loadingPermissions = false;
                $translate('ADMIN.ROLES.ERRORS.CANNOT_ASSIGN_REMOVE_PERMISSIONS_NOW').then(function (msg) {
                    alert(msg);
                })
            })
        }

        function removePermissions(permissions) {
            vm.flags.loadingPermissions = true;
            manageRolesService.removePermissionsAsync(vm.role.Id, permissions).then(function () {
                vm.flags.loadingPermissions = false;
                angular.forEach(permissions, function (p) {
                    var idx = vm.role.Permissions.indexOf(p);
                    if (idx !== -1) {
                        vm.role.Permissions.splice(idx, 1)
                    }
                })
            }, function () {
                vm.flags.loadingPermissions = false;
                $translate('ADMIN.ROLES.ERRORS.CANNOT_ASSIGN_REMOVE_PERMISSIONS_NOW').then(function (msg) {
                    alert(msg);
                })
            })
        }
    }

})();
(function () {
    'use strict';

    angular
        .module('app.admin')
        .controller('ManageRoleDialogController', manageRoleDialogController);

    /** @ngInject */
    function manageRoleDialogController($scope, $uibModalInstance, manageRolesService, $translate, editingRole) {

        $scope.role = {
            Id: null,
            Name: null
        }

        $scope.adding = true;

        if (editingRole && editingRole.Id) {
            angular.merge($scope.role, editingRole);
            if ($scope.role.Birthday) {
                $scope.role.Birthday = new Date($scope.role.Birthday);
            }
            $scope.adding = false;
        }

        $scope.loading = {
            processingRole: false
        };


        $scope.save = function () {
            if ($scope.roleForm.$invalid) {

                angular.forEach($scope.roleForm.$error, function (field) {
                    angular.forEach(field, function (errorField) {
                        errorField.$setTouched();
                    })
                });

                return;
            }

            $scope.loading.processingRole = true;

            if ($scope.adding) {
                manageRolesService.createRoleAsync($scope.role)
                    .then(function (role) {
                        $scope.loading.processingRole = false;

                        // close modal and pass back added role to dialog caller to param of resolve handler
                        $uibModalInstance.close(role);
                    }, function () {
                        $scope.loading.processingRole = false;

                        $translate('ADMIN.ROLES.DIALOG.ERRORS.CANNOT_ADD_NOW').then(function (msg) {
                            alert(msg);
                        })

                    })
            } else {
                manageRolesService.updateRoleAsync($scope.role.Id, { Name: $scope.role.Name })
                    .then(function () {
                        $scope.loading.processingRole = false;

                        // close modal and pass back updated role to dialog caller to param of resolve handler
                        $uibModalInstance.close($scope.role);
                    }, function () {
                        $scope.loading.processingRole = false;

                        $translate('ADMIN.ROLES.DIALOG.ERRORS.CANNOT_UPDATE_NOW').then(function (msg) {
                            alert(msg);
                        })

                    })
            }

        }

        // prevent close modal when saving
        $scope.$on('modal.closing', function (e) {
            if ($scope.loading.processingRole) {
                e.preventDefault();
            }
        })

    }

})();
(function () {
    'use strict';

    angular
        .module('app.admin')
        .controller('AdminUsersReportController', adminUsersReportController);

    /** @ngInject **/
    function adminUsersReportController($scope, $http, SVCS) {

        var vm = this;

        vm.report = {
            Items: []
        };

        // handle paging of table
        vm.paging = {
            totalItems: 0,
            page: 1,
            itemsPerPage: 50,
            maxSize: 5
        }

        // handle paged change by user
        vm.pageChanged = function () {

            searchUsers();

        }

        // handle users search by criterias
        vm.search = function () {
            if (vm.flags.loading) {
                return;
            }

            vm.paging.page = 1;
            searchUsers();
        }

        // handle users sort
        vm.sort = function (opts) {
            if (vm.flags.loading) {
                opts.cancel();
                return;
            }

            var sortNames = [];
            var sortDirections = [];
            if (opts.name.constructor === Array) {
                angular.forEach(opts.name, function (val) {
                    sortNames.push(val);
                    sortDirections.push(opts.direction);
                })
            } else {
                sortNames.push(opts.name);
                sortDirections.push(opts.direction);
            }

            vm.paging.page = 1;
            searchUsers(sortNames, sortDirections);
        }

        // handle search errors and loading flags
        vm.flags = {
            loading: false,
            searchFailed: false
        }

        // call first search to init table
        searchUsers();

        function searchUsers(sortNames, sortDirections) {
            vm.flags.searchFailed = false;
            vm.flags.loading = true;

            $http.get(SVCS.Report + '/api/reports/users', {
                params: {
                    Page: vm.paging.page,
                    Limit: vm.paging.itemsPerPage,
                    SortNames: sortNames,
                    SortDirections: sortDirections
                }
            }).then(function (response) {
                vm.flags.loading = false;
                vm.report = response.data;
                vm.paging.totalItems = vm.report.TotalUsers;
            }, function (errors) {
                vm.flags.loading = false;
                vm.flags.searchFailed = true;
            })
        }
    }


})();
