    /** @ngInject **/
    function paddingBottomByHeightOnResize() {
        return {
            restrict: 'A',
            link: function (scope, element) {
                var handler = function () {
                    setTimeout(function () {
                        var height = $(element.find('[height-calc]')[0]).outerHeight();
                        element.css('padding-bottom', height + 'px');
                    }, 200);
                }

                $(window).resize(handler)

                scope.$on('$destroy', function () {
                    $(window).off('resize', handler);
                })

                handler();
            }
        }
    }

