(function () {
    'use strict';

    angular
        .module('app.admin')
        .controller('AdminManageBookingsController', adminManageBookingsController);

    /** @ngInject **/
    function adminManageBookingsController(bookingStage, manageBookingsService, manageUsersService, manageRVsService, $timeout, stringhelper) {
        var vm = this;

        vm.stringhelper = stringhelper;

        vm.bookingStages = bookingStage;

        vm.bookings = [];

        // handle paging of table
        vm.paging = {
            totalItems: 0,
            page: 1,
            itemsPerPage: 20,
            maxSize: 5
        }

        // handle paged change by user
        vm.pageChanged = function () {

            searchBookings();

        }

        // handle users search by criterias
        vm.search = function () {
            if (vm.flags.loading) {
                return;
            }

            vm.paging.page = 1;
            searchBookings();
        }

        // handle users sort
        vm.sort = function (opts) {
            if (vm.flags.loading) {
                opts.cancel();
                return;
            }

            var sortNames = [];
            var sortDirections = [];
            if (opts.name.constructor === Array) {
                angular.forEach(opts.name, function (val) {
                    sortNames.push(val);
                    sortDirections.push(opts.direction);
                })
            } else {
                sortNames.push(opts.name);
                sortDirections.push(opts.direction);
            }

            vm.paging.page = 1;
            searchBookings(sortNames, sortDirections);
        }

        // search criterias
        vm.criterias = {
            id: null,
            state: null,
            ownerId: null,
            renterId: null
        }

        // handle search errors and loading flags
        vm.flags = {
            loading: false,
            searchFailed: false,
            loadingProfiles: false,
            loadProfilesFailed: false,
            loadingRVs: false,
            loadRVsFailed: false
        }

        // call first search to init table
        searchBookings();

        vm.getBookingStatusClass = _getBookingStatusClass;


        function searchBookings(sortNames, sortDirections) {
            if (vm.flags.loading) {
                return;
            }

            vm.flags.searchFailed = false;
            vm.flags.loading = true;

            manageBookingsService.searchBookingsAsync({
                Id: vm.criterias.id,
                State: vm.criterias.state,
                OwnerId: vm.criterias.ownerId,
                RenterId: vm.criterias.renterId,
                Page: vm.paging.page,
                Limit: vm.paging.itemsPerPage,
                SortNames: sortNames,
                SortDirections: sortDirections
            }).then(function (response) {
                vm.flags.loading = false;

                vm.bookings = response.Results;
                vm.paging.totalItems = response.Total;



                // load profiles, rvs of booking async
                if (vm.bookings.length) {
                    $timeout(function () {
                        var profileIds = [];
                        var rvIds = [];
                        angular.forEach(vm.bookings, function (booking) {
                            profileIds.push(booking.OwnerId);
                            profileIds.push(booking.RenterId);
                            rvIds.push(booking.RVId);
                        })

                        getProfiles(profileIds);
                        getRVs(rvIds);
                    })
                }

            }, function (errors) {
                vm.flags.loading = false;
                vm.flags.searchFailed = true;
            })
        }

        function getProfiles(profileIds) {
            vm.flags.loadingProfiles = true;
            vm.flags.loadProfilesFailed = false;
            manageUsersService.getUsersByIds(profileIds).then(function (users) {
                vm.flags.loadingProfiles = false;
                angular.forEach(vm.bookings, function (booking) {
                    booking.Owner = users[booking.OwnerId];
                    booking.Renter = users[booking.RenterId];
                })
            }, function () {
                vm.flags.loadingProfiles = false;
                vm.flags.loadProfilesFailed = true;
            })
        }

        function getRVs(rvIds) {
            vm.flags.loadingRVs = true;
            vm.flags.loadRVsFailed = false;
            manageRVsService.getByIdsAsync(rvIds).then(function (rvs) {
                vm.flags.loadingRVs = false;
                angular.forEach(vm.bookings, function (booking) {
                    booking.RV = rvs[booking.RVId];
                })
            }, function () {
                vm.flags.loadingRVs = false;
                vm.flags.loadRVsFailed = true;
            })
        }



        var defaultClass = [
            bookingStage.AwaitingOwnerApproval.Value,
            bookingStage.AwaitingPayment.Value
        ];

        var warningClass = [
            bookingStage.DeclinedByTheOwner.Value,
            bookingStage.DeclinedByTheRenter.Value
        ]

        var primaryClass = [
            bookingStage.DepositTaken.Value,
            bookingStage.PaymentInProcess.Value,
            bookingStage.FullPaymentReceived.Value,
            bookingStage.SecurityDepositTaken.Value,
            bookingStage.PendingOwnerCloseout.Value,
            bookingStage.PendingRenterCloseout.Value,
            bookingStage.PendingPayout.Value,
        ]

        var dangerClass = [
            bookingStage.CanceledByTheRenter.Value,
            bookingStage.CanceledByTheOwner.Value,
            bookingStage.PendingDispute.Value
        ]

        var successClass = [
            bookingStage.Completed.Value
        ]

        function _getBookingStatusClass(status) {
            if (defaultClass.indexOf(status) !== -1) {
                return 'label-default';
            } else if (warningClass.indexOf(status) !== -1) {
                return 'label-warning';
            } else if (primaryClass.indexOf(status) !== -1) {
                return 'label-primary';
            } else if (dangerClass.indexOf(status) !== -1) {
                return 'label-danger';
            } else if (successClass.indexOf(status) !== -1) {
                return 'label-success';
            } else {
                return 'label-info';
            }
        }
    }

})();
