(function () {
    'use strict';

    angular
        .module('app.booking')
        .factory('bookingService', bookingService);

    /** @ngInject */
    function bookingService(SVCS, $http, $q, $rootScope) {
        var authServer = SVCS.Auth;
        var profileServer = SVCS.Profile;
        var BookingServer = SVCS.Booking;
        var rvlistingServer = SVCS.RVListing;  
        var notificationServer = SVCS.Notification;

        var getIdProfile = function() {
           
            var deferer = $q.defer();

            $http.get(notificationServer + '/api/message/get-id-profile')
                .then(function(response) {
                    deferer.resolve(response.data);
                }), function(responseError) {
                    deferer.reject(responseError.data);

                }
            return deferer.promise;
        }

        var book =  function(bookingViewModel) {
            var deferer = $q.defer();

            $http.post(BookingServer + '/api/booking/book', bookingViewModel)
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })

            return deferer.promise;
        }

        var getRVDetail = function (rvname, city) {
            var deferer = $q.defer();

            $http.get(rvlistingServer + '/api/rvlistings/find-by-name', { params: { name: rvname, city: city } })
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })
            return deferer.promise;
        };

        var getThreadId = function (receiver, RVId) {
            var deferer = $q.defer();

            $http.get(notificationServer + '/api/message/get-id-thread?receiver=' + receiver  +
                '&RVId=' + RVId + '&BookingId=')
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })
            return deferer.promise;
        }

        var copyDataFromRVToBooking = function (rv, booking) {
            if (rv)
            {
                // owner, renter, rv
                booking.OwnerId = rv.OwnerId;
                booking.RVId = rv.Id;
                // high season, pricing
                booking.HSStartDate = rv.HSStartDate;
                booking.HSEndDate = rv.HSEndDate;
                booking.LowSeasonNight = rv.LowSeasonNight;
                booking.LowSeasonWeek = rv.LowSeasonWeek;
                booking.LowSeasonMonth = rv.LowSeasonMonth;
                booking.HighSeasonNight = rv.HighSeasonNight;
                booking.HighSeasonWeek = rv.HighSeasonWeek;
                booking.HighSeasonMonth = rv.HighSeasonMonth;
                // other pricings: mileage, generator, etc.
                booking.IsChargeMileage = rv.IsChargeMileage === true && rv.DailyKMAllowed > 0 && rv.SurchargePerExtraKM > 0;
                booking.DailyKMAllowed = rv.DailyKMAllowed;
                booking.SurchargePerExtraKM = rv.SurchargePerExtraKM;
                booking.IsGenerator = rv.IsGenerator === true && rv.FreeHoursPerDay > 0 && rv.ChargePerHourOver > 0;
                booking.FreeHoursPerDay = rv.FreeHoursPerDay;
                booking.ChargePerHourOver = rv.ChargePerHourOver;
                // location
                booking.StateOfRvLocation = rv.Location.State;
                // minumum rental day
                booking.RentalDay = rv.RentalDay;

                booking.Insured = rv.Insured;
            }
            
        }

        var getMyBooking = function (bookingId) {
            var deferer = $q.defer();

            $http.get(BookingServer + '/api/booking/my-booking/' + bookingId)
                .then(function (response) {
                    deferer.resolve(response.data);
                }), function (responseError) {
                    deferer.reject(responseError.data);
                }
            return deferer.promise;
        }

        function getCalendarByRV(ownerId, rvId) {
            return $http.get(SVCS.Calendar + '/api/calendars/get-by-owner/' + ownerId + "/" + rvId);
        }

        var getListProfile = function (listIds) {
            var deferer = $q.defer();

            $http.get(profileServer + '/api/user-profiles/get-list-profiles', { params: { Ids: listIds } })
                .then(function (response) {
                    deferer.resolve(response.data);
                }, function (responseError) {
                    deferer.reject(responseError.data);
                })
            return deferer.promise;
        }

        return {
            getRVDetail: getRVDetail,
            book: book,
            getThreadId: getThreadId,
            getIdProfile: getIdProfile,
            copyDataFromRVToBooking: copyDataFromRVToBooking,
            getCalendarByRV: getCalendarByRV,
            getMyBooking: getMyBooking,
            getListProfile: getListProfile
        };
    }
})();
