                CourtesyRequests: "",
                ParkAmenities: "",
                Weight: 0,
                HitchWeight: 0,
                HitchSize: "",
                SecurityDeposit: 0,
                HighSeasonNight: 0,
                HighSeasonWeek: 0,
                HighSeasonMonth: 0,
                LowSeasonNight: 0,
                LowSeasonWeek: 0,
                LowSeasonMonth: 0,
                IsRegisteredCollection: false,
                AccountName: "",
                HSStartDate: "2017-04-27T10:55:54.119Z",
                HSEndDate: "2017-04-27T10:55:54.119Z",
                IsChargeMileage: false,
