(function ()
{
    'use strict';

    angular
        .module('app.stringcommon', [])
        .factory('stringhelper', stringhelper);

    /** @ngInject */
    function stringhelper($state, SVCS, $stateParams)
    {
        var convertToAliasName = function (name) {
            var result = "";
            if (name)   
            {
                var result = name.replace(/ /g, '-');
                result = result.replace(/-+/g, '-');
                result = result.toLowerCase();
            }

            return result;
        }

        var convertToNumberPhoneString = function (number) {
            var result = "";
            if (!number || number.length > 12)
                return number;
            for (var i = 0; i < number.length; i++)
            {
                if (i == 3 || i == 7)
                    if (number[i] != '-')
                        result += '-';
                result += number[i];
            }
            return result;
        };

        var goToDetailPage = function (rvname, city) {
            var _city = convertToAliasName(city);

            return SVCS.Web + "/" + $stateParams.lang +  "/rv-rental/" + _city + "/" + rvname;
        }

        var addSpaceBeforeCapital = function (string) {
            return string.replace(/([a-z])([A-Z])/g, '$1 $2').trim();
        }

        return {
            convertToAliasName: convertToAliasName,
            convertToNumberPhoneString: convertToNumberPhoneString,
            goToDetailPage: goToDetailPage,
            addSpaceBeforeCapital: addSpaceBeforeCapital
        };
    }

})();
(function () {
    'use strict';

    angular
        .module('app.auth')
        .run(['AuthenticationService', '$rootScope', '$state', '$stateParams', function (_authService, $rootScope, $state, $stateParams) {
            var permissionsLoaded = false;

            $rootScope.$on('PERMISSIONS_LOADED', function () {
                permissionsLoaded = true;
            })

            $rootScope.$on("$stateChangeStart", function (event, toState, toParams, fromState, fromParams) {
                if (toState.protect === true) {
                    if (!_authService.IsAuthenticated) {
                        event.preventDefault();
                        if (fromState.name == '') {
                            $state.go('app.home').then(function () {
                                $rootScope.$emit('NEED_AUTH', angular.copy({
                                    fromState: fromState,
                                    fromParams: fromParams,
                                    toState: toState,
                                    toParams: toParams
                                }))
                            });
                        } else {
                            $rootScope.$emit('NEED_AUTH', angular.copy({
                                fromState: fromState,
                                fromParams: fromParams,
                                toState: toState,
                                toParams: toParams
                            }));
                        }

                    } else if (toState.permissions && toState.permissions.length) {
                        if (!_authService.HasPermissions(toState.permissions)) {
                            if (permissionsLoaded) {
                                event.preventDefault();
                                _authService.SignOut();
                                $state.go('app.auth_login');
                            } else {
                                event.preventDefault();
                                var evtCanceler = $rootScope.$on('PERMISSIONS_LOADED', function () {
                                    evtCanceler();
                                    if (!_authService.HasPermissions(toState.permissions)) {
                                        _authService.SignOut();
                                        $state.go('app.auth_login');
                                    } else {
                                        var params = angular.copy(toParams);
                                        $state.go(toState.name, params);
                                    }
                                })
                            }
                        }
                    }
                }
            });
        }]);
})();
(function () {
    'use strict';

    angular
        .module('app.auth')
        .directive('isUserNameAvailable', ['$q', '$http', '$timeout', 'SVCS', function ($q, $http, $timeout, SVCS) {
            return {
                require: 'ngModel',
                link: function (scope, elm, attrs, ctrl) {
                    var checkingTask = null;

                    var baseUrl = SVCS.Auth;

                    ctrl.$asyncValidators.isUserNameAvailable = function (modelValue) {

                        if (ctrl.$isEmpty(modelValue)) {
                            return $q.when();
                        }

                        var def = $q.defer();

                        if (checkingTask != null) {
                            $timeout.cancel(checkingTask);
                            checkingTask = null;
                        }
                        checkingTask = $timeout(function () {
                            $http.get(baseUrl + '/api/account/is-user-name-available', { params: { UserName: modelValue } }).then(function (response) {
                                if (response.data) {
                                    def.resolve();
                                } else {
                                    def.reject();
                                }
                            }, function (responseError) {
                                def.reject();
                            })
                        }, 500)

                        return def.promise;
                    };
                }
            };
        }])
        .directive('showRoute', ['AuthenticationService', '$rootScope', '$state', function (_authService, $rootScope, $state) {
            return {
                restrict: 'A',
                link: function (scope, element, attrs) {
                    handler();
                    $rootScope.$on('PERMISSIONS_LOADED', function () {
                        handler();
                    })
                    function handler() {
                        if (!attrs.uiSref && !attrs.showForRoute) {
                            element.hide();
                        } else {
                            var permissions = [];
                            if (attrs.uiSref) {
                                var uiSrefState = $state.get(attrs.uiSref);
                                if (uiSrefState) {
                                    if (uiSrefState.permissions) {
                                        permissions = uiSrefState.permissions;
                                    }
                                }
                            }
                            if (attrs.showForRoute) {
                                var state = $state.get(attrs.showForRoute);
                                if (state) {
                                    if (state.permissions) {
                                        permissions = permissions.concat(state.permissions);
                                    }
                                }
                            }

                            if (permissions.length) {
                                if (!_authService.HasPermissions(permissions)) {
                                    element.hide();
                                } else {
                                    element.show();
                                }
                            } else {
                                element.show();
                            }
                        }
                    }
                }
            }
        }])
        .directive('showPermissions', ['AuthenticationService', '$rootScope', '$state', function (_authService, $rootScope, $state) {
            return {
                restrict: 'A',
                scope: {
                    $$permissions: '=showPermissions'
                },
                link: function (scope, element, attrs) {
                    handler();
                    $rootScope.$on('PERMISSIONS_LOADED', function () {
                        handler();
                    })
                    function handler() {
                        if (scope.$$permissions.length) {
                            if (!_authService.HasPermissions(scope.$$permissions)) {
                                element.hide();
                            } else {
                                element.show();
                            }
                        } else {
                            element.show();
                        }
                    }
                }
            }
        }]);
})();
