(function () {
    'use strict';

    angular
        .module('rvezy')
        .run(run);

    /** @ngInject */
    function run(SVCS, $http, AuthenticationService, $stateParams, $rootScope, $state) {

        // ping all server to prevent them idle

        $http.get(SVCS.Auth + '/ping').then(function () { }, function () { })
        $http.get(SVCS.Profile + '/ping').then(function () { }, function () { })
        $http.get(SVCS.RVListing + '/ping').then(function () { }, function () { })
        $http.get(SVCS.Booking + '/ping').then(function () { }, function () { })
        $http.get(SVCS.Notification + '/ping').then(function () { }, function () { })
        $http.get(SVCS.Payment + '/ping').then(function () { }, function () { })
        $http.get(SVCS.Calendar + '/ping').then(function () { }, function () { })
        $http.get(SVCS.Scheduler + '/ping').then(function () { }, function () { })

        if (AuthenticationService.IsAuthenticated) {
            if (window.location.pathname === '/') {
                AuthenticationService.GetProfileAsync().then(function (profile) {
                    var lang = 'en';
                    switch (profile.Language) {
                        case 0:
                            lang = 'en';
                            break;
                        case 1:
                            lang = 'fr';
                            break;
                        default:
                            break;
                    }

                    if (lang != $stateParams.lang && !$state.current.abstract) {
                        $state.go('.', { lang: lang });
                    }
                })
            }           
        }
    }

})();
(function () {
    'use strict';

    angular
        .module('rvezy')
        .config(routeConfig)
        .run(scrollToTopOnChangeState);

    /** @ngInject */
    function routeConfig($stateProvider, $urlRouterProvider, $locationProvider) {
        $locationProvider.html5Mode(true);

        $urlRouterProvider.otherwise('/en/');

        /**
         * Layout Style Switcher
         *
         * This code is here for demonstration purposes.
         * If you don't need to switch between the layout
         * styles like in the demo, you can set one manually by
         * typing the template urls into the `State definitions`
         * area and remove this code
         */
        // Inject $cookies
        var $cookies;

        angular.injector(['ngCookies']).invoke([
            '$cookies', function (_$cookies) {
                $cookies = _$cookies;
            }
        ]);

        // State definitions
        $stateProvider
            .state('app', {
                url: '/:lang',
                abstract: true,
                template: '<div class="container-fluid" ui-view></div>'
            });

    }

    /** @ngInject */
    function scrollToTopOnChangeState($rootScope) {
        $rootScope.$on('$stateChangeSuccess', function () {
            $('html, body').animate({ scrollTop: 0 }, 0);
        })
    }

})();

