(function () {
    'use strict';

    angular
        .module('app.core.uploadControl', [])
        .directive('uploadControl', function () {
            return {
                restrict: 'A',
                controller: [
                    '$scope', function ($scope) {
                        this.$setControl = function (control) {
                            $scope.$control = function () {
                                return control;
                            }
                        }
                        this.$setFiles = function (files) {
                            $scope.$files = files;
                        }
                        this.$callback = function (callback) {
                            $scope.$evalAsync(callback);
                        }
                    }
                ],
                controllerAs: '$uploader',
                link: function (scope, element, attrs, ctrl) {
                    var $uploader = ctrl;

                    var input = $(element.find('input')[0]);

                    $uploader.$setControl(input);

                    var clicked = false;
                    element.click(function (event) {
                        if (!clicked) {
                            clicked = true;
                            input.click();
                            clicked = false;
                        }
                    });

                    input.change(function (event) {
                        $uploader.$setFiles(this.files);

                        if (attrs.onFileSelected) {
                            $uploader.$callback(attrs.onFileSelected);
                        }
                    });
                }
            }
        })
})();

(function () {
    'use strict';

    angular
        .module('app.core.stateGlobalLoading', [])
        .directive('globalLoading', ['$rootScope', function ($rootScope) {
            return {
                restrict: 'A',
                template: '<div class="global-loading"><div class="loader"></div></div>',
                link: function (scope, element, attrs) {
                    var loading = false;

                    var loadingStartEvtCanceller = $rootScope.$on('$stateChangeStart', function (event, toState, toParams, fromState, fromParams) {
                        if (toState.globalLoading) {
                            loading = true;
                        } else {
                            loading = false;
                        }
                        handleLoadingDisplay();
                    })

                    var loadingErrorEvtCanceller = $rootScope.$on('$stateChangeError', function (event, toState, toParams, fromState, fromParams, error) {
                        loading = false;
                        handleLoadingDisplay();
                    })

                    var loadingSuccessEvtCanceller = $rootScope.$on('$stateChangeSuccess', function (event, toState, toParams, fromState, fromParams) {
                        loading = false;
                        handleLoadingDisplay();
                    })

                    scope.$on('$destroy', function () {
                        loadingStartEvtCanceller();
                        loadingErrorEvtCanceller();
                        loadingSuccessEvtCanceller();
                    })

                    var loader = element.find('>.global-loading');
                    
                    function handleLoadingDisplay() {
                        if (loading) {
                            loader.addClass('loading');
                            element.addClass('loading');
                        } else {
                            loader.removeClass('loading');
                            element.removeClass('loading');
                        }
                    }
                }
            }
        }])
})();

(function () {
    'use strict';

    angular
        .module('app.core.elementLoading', [])
        .directive('elementLoading', function () {
            return {
                restrict: 'A',
                link: function (scope, element, attrs) {
                    if (attrs.elementLoading) {
                        element.append('<div class="element-loading"><div class="loader"></div></div>');

                        var loader = element.find('>.element-loading');

                        element.click(function (e) {
                            if (element.hasClass('loading')) {
                                e.preventDefault();
                                e.stopPropagation();
                            }
                        })

                        function correctContainerSize(count) {
                            loader.width(element.outerWidth());
                            loader.height(element.outerHeight());
                            if (count < 5) {
                                setTimeout(function () {
                                    correctContainerSize(count + 1)
                                }, 200);
                            }
                        }

                        if (scope.$eval(attrs.elementLoading)) {
                            loader.width(element.outerWidth());
                            loader.height(element.outerHeight());
                            loader.addClass('loading');
                            element.addClass('loading');
                            correctContainerSize(0);
                        }

                        scope.$watch(function () {
                            return scope.$eval(attrs.elementLoading);
                        }, function (val, old) {
                            if (val != old) {
                                if (val) {
                                    correctContainerSize(0);
                                    loader.addClass('loading');
                                    element.addClass('loading');
                                } else {
                                    loader.removeClass('loading');
                                    element.removeClass('loading');
                                }
                            }
                        })
                    }
                }
            }
        })
})();

