$templateCache.put("app/admin/cms/template/blog-tpl.html","<div class=\"top-header-banner-with-text\"><h1 class=\"page-header\">RVing With A Baby</h1><div \nclass=\"field field-name-field-banner-on-page-header field-type-image field-label-hidden\"><div class=\"field-items\"><div \nclass=\"field-item even\"><img typeof=\"foaf:Image\" class=\"img-responsive\" \nsrc=\"https://www.rvezy.com/sites/default/files/styles/banner_on_page_header/public/node-page-banner-images/rvezy_syriam_camping_trip798.jpg?itok=aOHS9CBX\" \nwidth=\"1524\" height=\"1016\" alt=\"\"></div></div></div></div><div class=\"main-container container\"><header role=\"banner\" \nid=\"page-header\"></header><div class=\"row\"><section class=\"col-sm-12\"><ol class=\"breadcrumb\"><li><a href=\"/en/blog\">\nBlogs</a></li><li><a href=\"/en/blogs/teamrvezy\">team_rvezy\'s blog</a></li><li class=\"active\">RVing With A Baby</li>\n</ol><a id=\"main-content\"></a><div class=\"region region-content\"><section id=\"block-system-main\" \nclass=\"block block-system clearfix\"><div about=\"/en/rving-baby\" typeof=\"sioc:Item foaf:Document\" \nclass=\"ds-3col-stacked-fluid node node-blog view-mode-full group-one-sidebar group-sidebar-right clearfix\"><div \nclass=\"group-header\"></div><div class=\"group-middle\"><div \nclass=\"field field-name-field-displayed-teaser-image field-type-computed field-label-hidden\"><div class=\"field-items\">\n<div class=\"field-item even\"></div></div></div><div \nclass=\"field field-name-submitted-by field-type-ds field-label-hidden\"><div class=\"field-items\"><div \nclass=\"field-item even\">Submitted 9 hours 30 min ago by <a href=\"/en/users/teamrvezy\" title=\"View user profile.\" \nclass=\"username\" xml:lang=\"\" about=\"/en/users/teamrvezy\" typeof=\"sioc:UserAccount\" property=\"foaf:name\" datatype=\"\">\nteam_rvezy</a>.</div></div></div><div class=\"field field-name-body field-type-text-with-summary field-label-hidden\">\n<div class=\"field-items\"><div class=\"field-item even\" property=\"content:encoded\"><p>&nbsp;</p><p><span \nstyle=\"color:#000000\">\nI did not camp or RV growing up. In fact, we didn’t travel much unless it was to a ball field to watch my Dad pitch. Fast forward to the present – I have a husband who did grow up camping and RVing. I am now getting the opportunity to RV regularly. I had two big concerns before our first trip – we have a 9-month old baby boy and a monster of a dog, and I just didn’t know how we would make it work.\n</span></p><p><span style=\"color:#000000\">\nBefore our first RV trip, I asked our friend to watch our dog (we will try bringing her next time). Because I am adamant about efficiency while maintaining comfort and ease I have created a master travel list that changes based on \n</span><span style=\"color:#000000\">mode</span><span style=\"color:#000000\">\n of transportation and accommodations. I will include this list in a separate blog post.</span><br>&nbsp;</p><p><span \nstyle=\"color:#000000\"><img alt=\"\" \nsrc=\"/sites/default/files/resize/remote/4a393aaafc0d6d063911807ba48e06e2-500x333.png\" \nstyle=\"height: 333px; width: 500px\" width=\"500\" height=\"333\"></span><br>&nbsp;</p><p><span style=\"color:#000000\">\nHere are some of the things we learned:</span></p><p><span style=\"color:#000000\"><strong>Childproof your RV</strong>\n – Piece of plywood to cover stairs, a baby gate to block off the&nbsp;</span><span style=\"color:#000000\">front</span>\n<span style=\"color:#000000\">\n of RV, and secure all projectiles. The RV is much smaller than our home so these were quick fixes we put </span><span \nstyle=\"color:#000000\">into</span><span style=\"color:#000000\"> action on the road.</span></p><p><span \nstyle=\"color:#000000\"><strong>Set up for sleeping success</strong>\n – We used our pack and play on top of the dining area. I packed a sheet, our crib blanket, and our son’s </span><span \nstyle=\"color:#000000\">favorite</span><span style=\"color:#000000\">\n stuffies. We used a white noise machine when the air conditioner wasn’t on. I believe this was key in our son’s sleeping success. We didn’t stick to a strict schedule but our son is an early to bed and early \n</span><span style=\"color:#000000\">riser</span><span style=\"color:#000000\">\n. If he goes to bed late he still wakes early.</span></p><p><span style=\"color:#000000\"><strong>Organize your stuff\n</strong>\n – I arranged and prepped the things we would need next to keep on top of mess and to help accommodate our son’s sleeping. For example – I would take night time clothing outside and all the ingredients for dinner or late night \n</span><span style=\"color:#000000\">s’mores</span><span style=\"color:#000000\"> to the cooler before his bedtime.</span>\n</p><p><span style=\"color:#000000\"><strong>Stuff we got great use of</strong> –</span></p><p><span \nstyle=\"color:#000000\"><em>The Broom</em>: Our son is very mobile and there was a lot of dirt floating around the RV.\n</span></p><p><span style=\"color:#000000\"><em>Pack and play</em>\n: Our son slept in this at night and in the day when we were busy we threw the <em>stroller bug net</em>\n over it and he played happily inside.</span></p><p><span style=\"color:#000000\"><em>Bike with baby seat:</em>\n I couldn’t help but notice that everyone had their bikes with them. Our son loved biking around and checking out all the other campers (don’t forget \n<em>helmets</em>!).</span></p><p><span style=\"color:#000000\"><em>Cooler and Ice</em>\n: We stored all our drinks outside to make the most of our fridge space.</span></p><p><span style=\"color:#000000\"><em>\nCoffee</em>: No explanation needed.</span></p><p><span style=\"color:#000000\"><strong>Things we need to pack next time\n</strong> –</span></p><p><span style=\"color:#000000\"><em>Rug</em> to wipe feet</span></p><p><span \nstyle=\"color:#000000\"><em>Outdoor bug tent</em> (we did all food prep and eating outside)</span></p><p><span \nstyle=\"color:#000000\"><strong>General first timer RV Tips</strong> –</span></p><ol><li><span style=\"color:#000000\">\nAlways check water levels before entering and exiting campgrounds.</span></li><li><span style=\"color:#000000\">\nKnow what </span><span style=\"color:#000000\">hookups</span><span style=\"color:#000000\">\n are offered at each RV site (so you can plan ahead and avoid a screaming baby).</span></li><li><span \nstyle=\"color:#000000\">\nMake sure black water valve is closed (we saw an unfortunate gentleman get covered in his own RV waste).</span></li>\n<li><span style=\"color:#000000\">Use campground showers (I used all the RV water trying to wash long hair).</span><br>\n&nbsp;</li></ol><p><span style=\"color:#000000\"><img alt=\"\" \nsrc=\"/sites/default/files/resize/remote/3e376e339dfd52f89eb118a1fc0c3f8e-500x333.png\" \nstyle=\"height: 333px; width: 500px\" width=\"500\" height=\"333\"></span><br>&nbsp;</p><p><span style=\"color:#000000\">\nI’ve become an avid camper since meeting my husband four and a half years ago and I love the promise of new adventures that RVing adds to our lives. My husband and I have been married two and a half years and I am a very proud mother to a \n</span><span style=\"color:#000000\">nine-and-a-half-month old</span><span style=\"color:#000000\">\n boy. I will be writing an ongoing blog chronicling our adventures in hopes of helping other </span><span \nstyle=\"color:#000000\">moms</span><span style=\"color:#000000\">\n and families take the RV vacationing plunge. Stay tuned to see how things turn out when we bring our dog!</span><br>\n&nbsp;</p><p><span style=\"color:#000000\"><img alt=\"\" \nsrc=\"/sites/default/files/resize/remote/a30f25419cb7da36882aace2e6d2184a-480x640.png\" width=\"480\" height=\"640\"></span>\n</p><p>&nbsp;</p></div></div></div></div></div></section></div></section></div></div>");
